#popup_email_subscription {
	display: none;
	position: fixed;
	top: 25%;
	left: 0;
	right: 0;
	margin: auto;
	text-align: center;
	z-index: 999999;
	@include media-breakpoint-down(sm) { /* max 767 */
		margin: 0 15px;
	}
	.image-banner-emailsub {
		@media (max-width:530px) and (min-width:320px) {
			height: 310px;
		}
	}
	img {
		border-radius: 10px;
	}
	.btn-close {
		bottom: 2.625rem;
		opacity: 1;
		position: absolute;
		right: auto;
		text-shadow: none; 
		font-weight: 600;
		left: 32.5%;
		@media (min-width: 2560px) {
			left: 36.5%;
		}
		@media (max-width: 1366px) {	
			left: 23.5%;
		}
		@include media-breakpoint-down(md) { /* max 991 */
			left: 13.5%;
		}
		@include media-breakpoint-down(sm) { /* max 767 */
			left: 8.5%;	
		}
		@include media-breakpoint-down(xs) { /* max 543 */
			left: 10.5%;
			bottom: 25px;
		}
		@media (max-width:530px) and (min-width:320px) {
			left: 28.5%;
			bottom: 25px;
		}
		.btn {
			border: medium none;
			color: $brand-primary;
			background: transparent;
			font-size: 17px;
			text-transform: capitalize;
			font-weight: 600;
			font-style: italic;
			@media (max-width: 543px) {
				font-size: 14px;
			}
			&:hover {
				border: none;
				background: transparent;
				opacity: 0.7;
				color: $gray-dark-color;
			}
		}
	}
	.close {
		position: absolute;
		right: 28.5%;
		top: 7px;
		text-shadow: none;
		opacity: 1;
		color: $brand-secondary;
		@media (min-width: 2560px) {
			right: 34%;
		}
		@media (max-width: 1366px) {
			right: 19.5%;
		}
		@include media-breakpoint-down(md) { /* max 991 */
			right: 8.5%;
		}
		@include media-breakpoint-down(sm) { /* max 767 */
			right: 1.5%;
		} 
		i {
			font-size: 22px;
			color: $brand-secondary; 
		}
		&:focus {
			outline: none;
		}
		&:hover {
			opacity: 0.7;
			color: $brand-secondary;
		}
	}
	.emailsub-text-wrapper {
		position: absolute;
		right: 53%;
		top: 50%;
		left: 29.5%;
		transform: translate(0, -50%);
		@media (min-width: 2560px) {
			right: 52%;
			left: 34.5%;
		}
		@media (max-width: 1366px) {
			right: 53%;
			left: 20%;
		}
		@include media-breakpoint-down(md) { /* max 991 */
			right: 55%;
			left: 9%;
		}
		@include media-breakpoint-down(sm) { /* max 767 */
			right: 55%;
			left: 2%;
		}
		h3 {
			&.title {
				font-family: $font-family;
				color: $brand-primary;
				font-weight: 700;
				font-size: 35px;
				line-height: normal;
				text-transform: capitalize;
				margin-bottom: 0.9375rem;
				font-style: italic;
				@media (max-width: 543px) {
					font-size: 24px;
					margin-bottom: 10px;
				}
				@media (max-width:530px) and (min-width:320px) {
					font-size: 14px;
					margin-bottom: 0;
				}
			}
		}
		.description {
			font-family: $font-family;
			font-size: 15px;
			font-weight: normal;
			color: $gray-dark-color;
			line-height: 20px;
			margin: 15px 15px 20px;
			@media (max-width: 543px) {
				font-size: 14px;
				margin: 0px 15px 10px;
			}
			@media (max-width:530px) and (min-width:320px) {
				font-size: 14px;
				margin: 0 0 10px;
			}
		}
		.input-group {
			margin-bottom: 15px;
			.form-control {
				height: 2.5rem;
				color: $gray;
				padding: 0.5rem 1.25rem;
				border: 1px solid $brand-primary;
				background: $light-color; 
				border-radius: 10px;
				&::placeholder {
					color: $gray;
				}
			}
		}
		.input-group-btn {
			.btn {
				font-family: $font-family;
				padding : 10px 30px;
				background-color: $brand-secondary;
				border: 1px solid $brand-secondary;
				font-size: 0.875rem;
				font-weight: 600;
				z-index: 9;
				cursor: pointer;
				transition: all 0.5s;
				color: $brand-primary; 
				border-radius: 25px;
				margin-bottom: 15px;
				height: 2.5rem;
				&:hover {
					background: $brand-primary;
					color: $brand-secondary;
					border: 1px solid $brand-primary;
				}
			}
		}
		.form-check-label {
			padding: 0;
			font-size: 0.875rem;
			color: $brand-primary;
			@media (max-width: 543px) {
				font-size: 13px;
			}
			@media (max-width:530px) and (min-width:320px) {
				font-size: 11px;
			}
			.form-check-input {
				margin: 0 0 0 0.625rem;
				display: none;
			}
			.custom-checkbox {
				border-radius: 0;
				background-color: $light-color;
				width: 0.9375rem;
				height: 0.9375rem;
				display: inline-block;
				position: relative;
				top: 0.1875rem;
				margin-right: 0.625rem;
				border: 1px solid $brand-primary;
			}
			.form-check-input {
				&:checked + {
					.custom-checkbox {
						&:before {
							font-family: 'Material Icons';
							content: '\E5CA';
							vertical-align: middle;
							font-size: 0.9375rem;
							line-height: 1;
							display: block;
							position: relative;
							left: -0.125rem;
							color: #472f1f;
						}
					}
				}
			}
		}
	}
}
#bg_popup_email_subscription {
	display: none;
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 99999;
	background-color: rgba(0, 0, 0, 0.7);
	top: 0;
	bottom: 0; 
}




