.products_block .owl-nav{
	margin: 0;
	position: absolute;
	right: 20px;
	top: -42px;
	@include media-breakpoint-down(md) { /* max 991 */
		right: 80px;
		top: -39px;
	}
	@media (max-width:530px) and (min-width:320px) {
		right: 50px;
	}
	.owl-prev {
		right: 2px;
		i {
			position: absolute;
			&:before {
				content: "\f104";
			}
		}
		&:after {
			content: '|';
			color: $gray-darker;
			font-size: 14px;
			padding: 0 20px;
			vertical-align: top;
			position: relative;
			top: 2px;
		}
	}
	.owl-next {
		left: 2px;
		i {
			position: absolute;
			&:before {
				content: "\f105";
			}
		}
	}
	> div {
		color: $gray-darker;
		border: none;
		background: transparent;
		font-size: 22px;
		height: 20px;
		margin: -10px 0 0 0px;
		position: absolute;
		text-align: center;
		transition: all 0.3s ease 0s;
		width: 20px;
		-webkit-transition: all 0.3s ease 0s;
		-moz-transition: all 0.3s ease 0s;
		-ms-transition: all 0.3s ease 0s;
		-o-transition: all 0.3s ease 0s;
		transition: all 0.3s ease 0s;
		&.disabled {
			opacity: 0.4;
		}
		i {
			
		}
		&:hover {
			-webkit-transition: all 0.3s ease 0s;
			-moz-transition: all 0.3s ease 0s;
			-ms-transition: all 0.3s ease 0s;
			-o-transition: all 0.3s ease 0s;
			transition: all 0.3s ease 0s;
			background: transparent;
			border: none;
			color: $brand-primary;
		}
	}
}

