#rvinstablock {
	position: relative;
	.title {
		display: none;
		@include media-breakpoint-down(xs) { /* max 543 */
			font-size: 18px;
		}
	}
	.item {
		a {
			position: relative;
			overflow: hidden;
			display: block;
			&:after {
				background: rgba(0, 0, 0, 0.3) none repeat scroll 0 0;
				height: 100%;
				opacity: 0;
				position: absolute;
				left: 0;
				content: "";
				text-align: center;
				top: 0;
				transform: scale(0);
				transition: all 0.5s ease 0s;
				width: 100%;
			}
			img {
				max-width: 100%;
				width: 100%;
				filter: grayscale(60%);
				height: 300px;
				transition: all 0.5s ease 0s;
			}
			.insta-icon {
				height: 100%;
				position: absolute;
				right: 0;
				text-align: center;
				top: 0;
				transform: scale(0);
				transition: all 0.3s ease 0s;
				width: 100%;
				z-index: 9;
				i {
					color: #fff;
					font-size: 30px;
					height: 30px;
					margin: -8px 0 0;
					position: relative;
					top: 50%;
					width: 30px;
					&:hover {
						border-radius: 10px;
						background: rgba(0, 0, 0, 0) radial-gradient(circle at 30% 107% , #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285aeb 90%) repeat scroll 0 0;
						box-shadow: 0 3px 10px rgba(0, 0, 0, 0.25);
					}
				}	
			}
			&:hover {
				&:after {
					transform: scale(1);
					opacity: 1;
				}
				img {
					transform: scale(1.1);
				}
				.insta-icon {
					transform: scale(1);
				}
			}
		}
		
		.main_content {
			bottom: 15px;
			left: 15px;
			position: absolute;
			right: 15px;
			color: $light-color;
			.likes {
				float: left;
				display: inline-block;
				vertical-align: middle;
				margin-left: 5px;
			}
			.comments {
				float: right;
				display: inline-block;
				vertical-align: middle;
				margin-right: 5px;
			}
		}
	}
	
	.owl-carousel {
		.owl-nav {
			display: none;
			left: 0;
			margin: 0;
			position: absolute;
			right: 0;
			top: 40%;
			z-index: 1;
			.owl-prev { 
				left: 0;
				&:hover { 
					opacity: 1;
				}
			}
			.owl-next { 
				right: 0;
				&:hover { 
					opacity: 1;
				}
			}
			div {
				width:30px;
				height:30px;
				padding: 2px;
				position: absolute;
				margin: 0;
				border:1px solid $base-border-color;
				-webkit-transition: all 0.3s ease 0s;
				-moz-transition: all 0.3s ease 0s;
				-ms-transition: all 0.3s ease 0s;
				-o-transition: all 0.3s ease 0s;
				transition: all 0.3s ease 0s;
				font-size: 0;
				&:hover {
					-webkit-transition: all 0.3s ease 0s;
					-moz-transition: all 0.3s ease 0s;
					-ms-transition: all 0.3s ease 0s;
					-o-transition: all 0.3s ease 0s;
					transition: all 0.3s ease 0s;
					color: $link-hover-color;
					border-color: $link-hover-color;
				}
			}
		}
	}
}


