#rvtestimonials {
	float: left;
	margin-top: 0;
	padding: 0;
	width: 100%;
	background-color: $light-color;
	margin-bottom: 30px;
	border-radius: 10px;
	padding: 20px 0 0;
	.testimonial_inner {
		margin: 20px 0 30px;
		text-align: center;
		.testimonial-item {
			padding: 10px 15px 20px 15px;
			.testimonial-image {
				img { 
					max-width: 100%;
					border: 2px solid $gray;
					border-radius: 50%; 
				}
			}
			.testimonial-content {
				position: relative;
				padding: 20px 0 0;
				.testimonial-name {
					display: block;
					font-size: 18px;
					font-weight: 500;
					line-height: 18px;
					margin-bottom: 10px;
					text-transform: capitalize;
					color: $gray-darker;
					&:before{
						content: "-";
						margin-right: 5px;
					}
				}
				.testimonial-designation {
					font-size: 16px;
					line-height: 16px;
					display: block;
					margin: 10px 0; 
					color: $brand-primary;
					color: $gray;
					text-transform: capitalize;
				}
				hr {
					margin-top: 20px;
					margin-bottom: 20px;
					border: 0;
					border-top-width: 0px;
					border-top-style: none;
					border-top-color: currentcolor;
					border-top: 1px solid #eee;
				}
				.testimonial-text {
					font-size: 13px;
					line-height: 22px;
					letter-spacing: 1px;
					margin: 0 0 10px;
					color: $gray;
					position: relative;
					@include media-breakpoint-down(xs) { /* max 543 */
						font-size: 14px;
					}
				}
			}
		}
	}
	.owl-carousel {
		.owl-nav {
			left: 0;
			margin: 0;
			position: absolute;
			right: 0;
			top: 40%;
			z-index: 1;
			display: none;
			.owl-prev { 
				left: 0;
				i {
					&:before {
						content: "\f104";
					}
				}
				&:hover { 

				}
			}
			.owl-next { 
				right: 0;
				i {
					&:before {
						content: "\f105";
					}
				}
				&:hover { 

				}
			}
			div {
				background: $light-color none repeat scroll 0 0;
				border: 1px solid $base-border-color;
				font-size: 18px;
				height: 30px;
				margin: 30px 0 0;
				padding: 5px;
				position: absolute;
				text-align: center;
				transition: all 0.3s ease 0s;
				width: 30px;
				-webkit-transition: all 0.3s ease 0s;
				-moz-transition: all 0.3s ease 0s;
				-ms-transition: all 0.3s ease 0s;
				-o-transition: all 0.3s ease 0s;
				&:hover {
					-webkit-transition: all 0.3s ease 0s;
					-moz-transition: all 0.3s ease 0s;
					-ms-transition: all 0.3s ease 0s;
					-o-transition: all 0.3s ease 0s;
					transition: all 0.3s ease 0s;
					color: $brand-primary;
					border-color: $brand-primary;
				}
			}
		}
		.owl-dots {
			bottom: auto;
			display: block;
			text-align: center;
			opacity: 1;
			position: absolute;
			left: 0;
			right: 0;
			bottom: -10px;
			margin-bottom: 0px;
			-webkit-transition: all 0.3s ease 0s;
			-moz-transition: all 0.3s ease 0s;
			-ms-transition: all 0.3s ease 0s;
			-o-transition: all 0.3s ease 0s;
			transition: all 0.3s ease 0s;
			.owl-dot {
				display: inline-block;
				vertical-align: middle;
				span {
					border: 1px solid $brand-primary;
					border-radius: 0;
					display: block;
					height: 12px;
					margin: 0 2px;
					border-radius: 20px;
					width: 12px;
					cursor: pointer;
					background: $brand-primary;
				}
				&.active, &:hover {
					span  {
						background: $light-color;
						border: 1px solid $brand-primary;
					}
				}
				&.active {
					span  {
						cursor: default;
					}
				}
			}
		}
	}
}



