#custom-text {
  background: white;
  border-radius: 2px;
  margin-bottom: rem(24px);
  padding: rem(50px) rem(50px);
  text-align: center;
  h3 {
    text-transform: uppercase;
    color: $gray-darker;
    font-size: rem(25px);
    font-weight: 700;
  }
  p {
    color: $gray-darker;
    font-weight: 400;
    font-size: 1.1em;
    .dark {
      color: $gray;
      font-weight: 400;
    }
  }
}
.page-content.page-cms{
  background: white;
  text-align: justify;
  .cms-box{
    img{
      max-width: 100%;
    }
  }
}
@include media-breakpoint-down(md) {
  #block-cmsinfo {
    padding: $medium-space $large-space;
  }
}


#cms {
  .page-heading {
    display: none;
  }
  #wrapper {
    .block-cms {
      padding-bottom: 20px;
    }
    h1 {
      margin-bottom: 25px;
      color: $gray-darker;
    }
    h2{
      color: $gray-darker;
    }
    h3 {
      font-size: 16px;
      border-bottom: none;
      margin: 0;
      color: $gray;
      padding: 0 0 17px 0;
    }
    p {
      line-height: 18px;
    }
    .list-1 {
      li {
        padding: 4px 0 6px 0;
        border-top: 1px solid $base-border-color;
        &:first-child {
          border: none;
        }
        em {
          font-size: 20px;
          line-height: 20px;
          padding-right: 15px;
          vertical-align: -2px;
        }
      }
    }
    img {
      margin: 4px 0 17px;
      max-width: 100%;
      height: auto;
    }
    .testimonials {
      border: 2px solid $base-border-color;
      background: $light-color;
      margin: 4px 0 13px 0;
      position: relative;
      .inner {
        border: 1px solid $light-color;
        padding: 17px 12px 20px;
        span {
          &.before {
            content: "\201C";
            font-family: Georgia, serif;
            font-size: 40px;
            color: #999;
            float: left;
            position:relative;
            top:9px;
            margin: 0 6px 0 0;
          }
          &.after {
            content: "\201C";
            font-family: Georgia, serif;
            font-size: 40px;
            color: #999;
            float: right;
            margin: 12px 10px 0 0;
          }
        }
      }
      &:after, &:before {
        top: 100%;
        left: 32px;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
      }
      &:before {
        border-color: rgba(226, 226, 226, 0);
        border-top-color: $base-border-color;
        border-width: 11px;
        margin-left: -11px;
      }
      &:after {
        border-color: rgba(255, 255, 255, 0);
        border-top-color: #ffffff;
        border-width: 8px;
        margin-left: -8px;
      }
      & + p {
        padding-left: 45px;
        margin-bottom: 18px;
      }
    }
    p.bottom-indent {
      margin-bottom: 18px;
    }
  }
}


