.sitemap {
  position: relative;
  padding: rem(32px);
  border: 1px solid $base-border-color;
  ul.tree {
    li {
      margin: 0;
      padding: 5px 0 0 25px;
      border-left: 1px solid #d6d4d4;
      line-height: 21px;
      background: url(../img/sitemap-horizontal.png) no-repeat left 15px transparent;
    }
    > li {
      margin: 0 0 0 10px;
      ul {
        margin: 0 0 0 10px;
      }
    }
  }
}


