#products,  .featured-products,.product-accessories, .products_block {
    .products_block_inner {
        position: relative;
        text-align: center;
    }
    .product-miniature {
        padding: 0 15px;
        .product-container {
            position: relative;
            margin: 0 auto 20px;
            -webkit-transition: all 0.3s ease 0s;
            -moz-transition: all 0.3s ease 0s;
            -ms-transition: all 0.3s ease 0s;
            -o-transition: all 0.3s ease 0s;
            transition: all 0.3s ease 0s;
            @include media-breakpoint-down(xs) { /* max 543 */
                max-width: 370px;
            }
            .thumbnail-container {
                position: relative;
                .thumbnail-inner {
                    position: relative;
                    overflow: hidden;  
                    border-radius: 10px;  
                    background-color: $light-color;
                    position: relative;
                }
                .product-thumbnail {
                    display: block;
                    text-align: center;
                    position: relative; 
                    img {
                        margin: 0 auto;
                        max-width: 100%;
                        -webkit-transition: all 0.7s ease 0s;
                        -moz-transition: all 0.7s ease 0s;
                        -ms-transition: all 0.7s ease 0s;
                        -o-transition: all 0.7s ease 0s;
                        transition: all 0.7s ease 0s;
                        &.img_1 {
                            left: 0;
                            position: absolute;
                            right: 0;
                            top: 0;
                            opacity: 0;
                            -moz-transform: scale(0,1);
                            -webkit-transform: scale(0,1);
                            -o-transform: scale(0,1);
                            -ms-transform: scale(0,1);
                            transform: scale(0,1);
                        }
                    }
                    &:after{
                        content: "";
                        position: absolute;
                        background: rgba(255,255,255,0.1);
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        transform: scale(0);
                        -moz-transform: scale(0);
                        -webkit-transform: scale(0);
                        -o-transform: scale(0);
                        -ms-transform: scale(0);
                        transition: all 0.5s;
                        -webkit-transition: all 0.5s;
                        -moz-transition: all 0.5s;
                        -o-transition: all 0.5s;
                    }
                }
                .product-buttons {
                    cursor: pointer;
                    display: inline-block;
                    left: 0;
                    right: 0;
                    position: absolute;
                    text-align: center;
                    bottom: 100px;
                    overflow: hidden;
                    @include media-breakpoint-down(sm) { /* max 767*/
                        bottom: 100px;
                    }
                    .btn-tertiary, .btn-secondary, .btn-primary {
                        height: 35px;
                        padding: 0;
                        transition: all 0.5s ease 0s;
                        width: 35px;
                        border-radius: 50%;
                        border: 1px solid $brand-primary;

                    } 
                    > div {
                     display: inline-block;
                     opacity: 0;
                     transform: translateY(60px);
                     vertical-align: middle;
                     transition: all 0.3s ease 0s;
                     -webkit-transition: all 0.3s ease 0s;
                     -moz-transition: all 0.3s ease 0s;
                     -ms-transition: all 0.3s ease 0s;
                     -o-transition: all 0.3s ease 0s;
                     &:nth-child(1) {
                        transition-delay: 0.3s;
                    }
                    &:nth-child(2) {
                        transition-delay: 0.4s;
                    }
                    &:nth-child(3) {
                        transition-delay: 0.5s;
                    }
                    &:nth-child(4) {
                        transition-delay: 0.6s;
                    }
                }
                .wishlist_btn {
                 .btn {
                     background-image: url(../../assets/img/sprite.png);
                     background-color: $brand-primary;
                     background-position: -68px -476px;
                     &:hover {
                         background-color: $brand-secondary;
                         background-position: -120px -476px;
                         border: 1px solid $brand-secondary;

                     }
                     span {
                        display: none;
                    }
                    i {
                        display: none;
                    }
                }
            }
            .product-cart-btn {
                .btn {
                   background-image: url(../../assets/img/sprite.png);
                   background-color: $brand-primary;
                   background-position: -69px -524px;
                   &:hover {
                    background-color: $brand-secondary;
                    background-position: -120px -524px;
                    border: 1px solid $brand-secondary;
                }
                i{
                    display: none;
                }
                span {
                    display: none;
                }
            }
        }
        .product-quick-btn {
            .btn {
                background-image: url(../../assets/img/sprite.png);
                background-color: $brand-primary;
                background-position: -69px -431px;
                &:hover {
                    background-color: $brand-secondary;
                    background-position: -121px -431px;
                    border: 1px solid $brand-secondary;
                }
                i{
                    display: none;
                }
                .lblquickview {
                    display: none;
                }
            }
        }
        .compare {
          .btn {
             background-image: url(../../assets/img/sprite.png);
             background-color: $brand-primary;
             background-position: -69px -571px;
             &:hover {
                background-color: $brand-secondary;
                background-position: -121px -571px;
                border: 1px solid $brand-secondary;
            }
        }
        span {
            display: none;
        }
        i {
            display: none;
        }
    }
}
}
.productcountdown {
   margin: 10px auto;
   background: transparent none repeat scroll 0 0;
   border-radius: 0;
   left: 0;
   padding: 0;
   position: absolute;
   right: 0;
   text-align: center;
   top: 0;
   vertical-align: top;
   opacity: 0;
   transform: scale(0);
   -webkit-transition: all 0.7s ease 0s;
   -moz-transition: all 0.7s ease 0s;
   -ms-transition: all 0.7s ease 0s;
   -o-transition: all 0.7s ease 0s;
   transition: all 0.7s ease 0s;
}
.product-description {
    text-align: center;
    padding: 0 2px 20px 1px;
    .product-title {
        margin-bottom: 10px;
        a {
            font-size: 15px;
            text-decoration: none;
            text-align: center;
            font-weight: 500;
            color: $brand-primary;
            font-family: $font-family;
        }
    }
    .product-price-and-shipping {
        color: $brand-primary;
        font-weight: 700;
        font-size: 15px;
        margin-bottom: 0px;
        > span {
            display: inline-block;
            vertical-align: middle;
        }
        .regular-price {
            color: $gray-light;
            text-decoration: line-through;
            font-weight: 500;
        }
        .discount-percentage {
            background: #d9d9d9;
            font-weight: normal;
            line-height: 10px;
            margin: 0 5px;
            padding: 4px;
            color: $gray-darker;
            font-size: 12px;
        }
        .price {
            color: $brand-primary;
            font-weight: 700;
        }
    }
    .product-desc {
        margin-bottom: rem(10px);
        display: none;
    }
    .variant-links {
        margin-bottom: rem(10px);
        position: relative;
        width: 100%;
        display: none;
        .count {
            color: $gray-dark;
            font-weight: 700;
            position: relative;
            vertical-align: middle;
        }
    }
    .comments_note {
        margin-bottom: 5px;
    }
}
.product-flags {
    > li {
        opacity: 1;
        font-family: $font-family;
        font-size: 14px;
        color: $brand-primary;
        font-weight: 600;
        position: absolute;
        text-transform: uppercase;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
    }
    .discount, .on-sale{
        font-family: $font-family;
        right: 10px;
        top: 10px;
        transform: translateX(0);
        left: auto;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
    }
    .new {
        right: auto;
        top: 10px !important;
        left: 10px;
        color: $brand-secondary;
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0);
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
    }
    .online-only {
        display: none;
        left: 10px;
        bottom: 10px;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
    }
    .pack {
        right: 10px;
        top: 30px;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
    }
}
&:hover {  
  .thumbnail-inner {
    box-shadow: 0 0 10px 2px rgba(0,0,0,.11);
    .product-thumbnail {
        img {
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
            &.img_1 {
                opacity: 1;
                -moz-transform: scale(1,1);
                -webkit-transform: scale(1,1);
                -o-transform: scale(1,1);
                -ms-transform: scale(1,1);
                transform: scale(1,1);
            }
        }
    }
    .product-buttons {
        > div {
            opacity: 1;
            transform: translateX(0px);
        }
        .product-cart-btn {
            .btn {

            }
        }
        .product-quick-btn {
            .quick-view {

            }
        }
    }
    .productcountdown {
        opacity: 1;
        transform: scale(1);
    }
    .product-flags {
        > li {
            opacity: 0;
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
        }
        .discount, .on-sale {
            transform: translateX(100%);
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
        }
        .new {
            -webkit-transform: translateX(-100%);
            -moz-transform: translateX(-100%);
            -ms-transform: translateX(-100%);
            -o-transform: translateX(-100%);
            transform: translateX(-100%);
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;

        }
    }
}
.product-description {
    .product-title {
        a {
            color: $gray-dark-color;
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
        }
    }
}
}
}
@include media-breakpoint-only(xl) { /* min 1200 */
    &.col-xl-3:nth-child(4n+1) {
        clear: left;
    }
    &.col-xl-4:nth-child(3n+1) {
        clear: left;
    }
}
@include media-breakpoint-only(lg) { /* min 992 max 1199 */
    &.col-lg-4:nth-child(3n+1) {
        clear: left;
    }
    &.col-lg-6:nth-child(2n+1) {
        clear: left;
    }
}
@include media-breakpoint-only(md) { /* min 768 max 991 */
    &.col-md-4:nth-child(3n+1) {
        clear: left;
    }
}
@include media-breakpoint-only(sm) { /* min 544 max 767 */
    &.col-sm-6:nth-child(2n+1) {
        clear: left;
    }
}
}
.all-product-link {
    clear: both;
    font-weight: 700;
    margin-top: rem(24px);
    margin-bottom: rem(24px);
}
}

