
.rv_category {
	float: left;
	vertical-align: top;
	position: relative;
	width: 100%;
	margin: 0 0 30px;
	@include media-breakpoint-down(xs) { /* max 543 */
		margin: 0 0 15px;
	}
	.title_tab_hide_show {
		padding: 10px 20px 10px 20px;
		font-family: $font-family;
		font-size: 16px;
		margin-right: 10px;
		color: $light-color;
		background: #05a75c;
		border-radius: 3px;
		-moz-border-radius: 3px;
		-webkit-border-radius: 3px;
		-khtml-border-radius: 3px;
		text-transform: uppercase;
		float: left;
		@include media-breakpoint-down(sm) { /* max 767 */
			float: left;
		}
	}
	.rv_cat_content {
		background: $light-color none repeat scroll 0 0;
		float: left;
		overflow: hidden;
		width: 100%;
		border: 1px solid #e5e5e5;
		border-radius: 10px;
		-moz-border-radius: 10px;
		-webkit-border-radius: 10px;
		-khtml-border-radius: 10px;
		margin: 0 0 5px
	}
	.list_carousel {
		clear: both;
	}
	ul {
		.product-list {
			li {
				float: left;
				margin-left: 20px;
				margin-top: 20px;
				margin-bottom: 35px;
				position: relative;
				height: auto;
				width: 240px;
				text-align: center;
				position: relative;
				ul {
					li {
						float: left;
						margin-left: 0;
						margin: 5px 0;
						position: relative;
						height: auto;
						width: auto;
						text-align: left;
						clear: both;
					}
				}
				.cat-img {
					line-height: 8px;
					border-radius: 5px;
					-moz-border-radius: 5px;
					-webkit-border-radius: 5px;
					-khtml-border-radius: 5px;
				}
				.title {
					margin: 0;
					padding: 10px 0;
					text-align: left;
					position: absolute;
					bottom: 10px;
					left: 30px;
					a {
						position: relative;
						color: #fff;
						font-size: 24px;
						font-family: $font-family;
						text-transform: uppercase;
						img {
							border-radius: 5px;
							-moz-border-radius: 5px;
							-webkit-border-radius: 5px;
							-khtml-border-radius: 5px;
						}
					}
				}
			}
		}
	}
	.cat-img {
		a {
			float: left;
			position: relative;
			width: 100%;
			img {
				display: block;
				max-width: 100%;
				margin-left: -30px;
				transition: all 400ms ease-in-out 0s;
				-moz-transition: all 400ms ease-in-out 0s;
				-o-transition: all 400ms ease-in-out 0s;
				-webkit-transition: all 400ms ease-in-out 0s;
			}
		}
	}
}
#rv_category {
	.sub-cat {
		ul {
			float: left;
			margin: 0;
			width: 100%;
			z-index: 1;
		}
	}
	.wt-block-title {
		h3 {
			font-size: 24px;
			color: $gray-darker;
			border-bottom: 3px solid #f12a43;
			padding-bottom: 7px;
			float: left;
			margin-bottom: 15px;
		}
	}
	#rv_cat_featured {
		.item {
			.content {
				.rvcat-content {
					padding: 10px;
					text-align: left;
					float: left;
					@media(max-width:1200px) {
						padding: 10px 15px;
					}
					@include media-breakpoint-down(sm) { /* max 767 */
						padding: 10px;
					}
				}
			}
		}
	}
}
#rv_cat_featured {
	.content {
		float: left;
		width: 100%;
		overflow: hidden;
		.title {
			a {
				color: $brand-primary;
				font: 500 16px/30px $font-family;
				letter-spacing: 0.4px;
				margin: 0 0 10px;
				position: relative;
				text-transform: capitalize;
				padding: 0 0 6px;
				float: left;
				width: 100%;
				&:after {
					background: #e5e5e5;
					bottom: 0;
					content: "";
					height: 1px;
					left: 0;
					position: absolute;
					transition: width 400ms ease-in-out 0s;
					width: 50px;
				}
			}
		}
		.sub-cat {
			ul {
				li {
					a {
						color: $gray;
						font: 300 14px/24px $font-family;
						letter-spacing: 0.4px;
						text-transform: capitalize;
						position: relative;
						transition: all 0.3s ease 0s;
						-webkit-transition: all 0.3s ease 0s;
						-moz-transition: all 0.3s ease 0s;
						-ms-transition: all 0.3s ease 0s;
						-o-transition: all 0.3s ease 0s;
						@include media-breakpoint-down(sm) { /* max 767 */
							font-size: 13px;
						}
						&:before {
							content: "\E5CC";
							font-family: "Material Icons";
							font-size: 20px;
							float: left;
						}
						&:hover {
							color: $brand-secondary;
							transition: all 0.3s ease 0s;
							-webkit-transition: all 0.3s ease 0s;
							-moz-transition: all 0.3s ease 0s;
							-ms-transition: all 0.3s ease 0s;
							-o-transition: all 0.3s ease 0s;
						}
					}
				}
			}
		}
		&:hover {
			.cat-img {
				a { 
					img {
						margin: 0;
					}
				}
			}
			.title { 
				a {
					color: $gray-darker;
				}
			}
		}
	}
	.item {
		float: left;
		width: 100%;
	}
	.cat-img {
		padding: 0;
		float: left;
	}
}
.caroufredsel_wrapper {
	@media (min-width: 768px) and (max-width: 991px) {
		height: 250px !important;
	}
}
#rvcategorytabs {
	.tab-content {
		.rvcategory {
			@include media-breakpoint-down(xs) { /* max 543 */
				padding: 0;
			}
		}
	}
}



