/*---------compare---------*/
.compare {
  > a {
    color: $brand-primary;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    &.bt_compare {
     font-size: 14px;
     padding: 0 0 0 25px;
     position: relative;
     cursor: pointer;
     &:after {
       background: url(../../assets/img/sprite.png) no-repeat -16px -300px transparent;
       content: "";
       height: 20px;
       left: 0px;
       position: absolute;
       top: 0;
       width: 20px;
     } 
   }
   .compare-title {
   }
   i {
    color: $light-color;
  }
  .total-compare-val {
   font-size: 16px;
   display: inline-block;
 }
 &:hover {
  color: $brand-secondary;
  &.bt_compare { 
    &:after { 
      background-position: -75px -300px;
    }
  }
}
}
}


.rvcomparetitle {
  color: $gray-darker;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  margin: 0 auto 25px;
  padding: 0;
  position: relative;
  text-align: left;
  text-transform: capitalize;
}

table#product_comparison {
  border: 1px solid $gray;
  td {
    border-color: $gray;
    background: $light-color;
    @media (max-width:530px) and (min-width:320px) {
      position: relative;
      white-space: normal;
      float: left;
      width: 100%;
      text-align: center;
      border: none; 
    }
  }
  tr td.empty > span {
    color: $gray-darker;
    font: 600 16px/22px $font-family;
    text-transform: uppercase; 
  }
  .rv_empty {
    &.compare_extra_information {
      vertical-align: bottom;
      @media (max-width:530px) and (min-width:320px) {
        border-bottom: 1px solid $gray;
        width: 100%;
      }
      > span {
        font-weight: 500;
        font-size: 16px;
        color: $gray-darker;
      }
    }
  }
  .product-block {
    text-align: center;
    padding: 15px;
    @include media-breakpoint-down(md) { /* max 991 */ 
      padding: 10px;
    }
  }
  .remove {
    padding: 0 0 15px 0;
    a {
      font-size: 23px;
      line-height: 23px;
      color: $gray-dark-color;
      &:hover {
        color: $gray;
      }
    }
  }
  .thumbnail-container {
    display: inline-block;
    position: relative;
    .product-thumbnail {
      padding: 5px;
      border: 1px solid #e1ddd8;
      display: inline-block;
      margin: 0 0 10px 0;
      img {
        width: 100%;
        max-width: 250px;
      }
    }
  }
  .product-description {
    .product-title {
      a {
        color: $gray-dark-color;
        font-size: 14px;
        font-family: $font-family;
        font-weight: 500;
        letter-spacing: 0.5px;
        text-transform: capitalize;
        &:hover {
          color: $brand-primary;
        }
      }
    }
    .product-price-and-shipping {
      color: $brand-primary;
      font-weight: 500;
      font-size: 14px;
      margin: 0 0 15px;
      line-height: 20px;
      text-align: center;
      letter-spacing: .2px;
    }
    .product-desc {
      padding: 0 0 10px 0;
      margin: 0;
      color: $gray-darker;
      font-size: 14px;
    }
    .highlighted-informations {
      padding: 0 0 10px 0;
      margin: 0;
    }
    .button{
      &.ajax_add_to_cart_button {
        color: $brand-secondary;
        background-color: $brand-primary;
        border-color: transparent;
        font-size: 14px;
        letter-spacing: 1px;
        line-height: 24px;
        padding: 6px 12px;
        overflow: hidden;
        transition: all 0.5s;
        border-radius: 5px;
        &:hover {
          color: $brand-primary;
          background-color: $brand-secondary;
          border-color: transparent;
        }
      }
    }
  }
}





