#rvtopbanners {
	position: relative;
	margin-bottom: 40px;
	.item {
		padding: 0 15px;
		text-align: center; 
		.rvtopbanners-container {
			overflow: hidden;
			vertical-align: middle;
			position: relative;
			@include media-breakpoint-down(sm) { /* max 767 */
				text-align: center;
			}
			.topbanner-image {
				overflow: hidden;
				position: relative;
				vertical-align: middle;
				img {
					min-width: 100%;
				}
			}
			.topbanner-detail {
				background: $brand-primary;
				bottom: 0px;
				transform: translateY(40px);
				display: block;
				font-family: $font-family;
				left: 0;
				margin: auto;
				opacity: 0;
				position: absolute;
				right: 0;
				text-align: center;
				top: auto;
				transition: all 0.4s ease 0s;
				transition: all 0.4s ease 0s;
				-moz-transition: all 0.4s ease 0s;
				-webkit-transition: all 0.4s ease 0s;
				-khtml-transition: all 0.4s ease 0s;
				-o-transition: all 0.4s ease 0s;
				@include media-breakpoint-down(md) { /* max 991 */
				}
				@include media-breakpoint-down(sm) { /* max 767 */

				}
				@include media-breakpoint-down(xs) { /* max 543 */


				}
				@media (max-width:530px) and (min-width:320px) {
					
				}
				.topbanner-title {
					color: $brand-secondary;
					display: block;
					font-size: 17px;
					padding: 8px;
					font-weight: 500;
					font-family: $font-family;
					text-transform: capitalize;
					@include media-breakpoint-down(md) { /* max 991 */

					}
					@include media-breakpoint-down(sm) { /* max 767 */

					}
					@include media-breakpoint-down(xs) { /* max 543 */
					}
					@media (max-width:530px) and (min-width:320px) {
						font-size: 14px;
						padding: 2px;
					}
				}
				.topbanner-info {
					display: none;
					font-size: 22px;
					margin-bottom: 10px;
					color: $gray-darker;
					font-family: $font-custom;
					display: none;
					@include media-breakpoint-down(md) { /* max 991 */
						font-size: 22px;
						margin-bottom: 0;
					}
					@include media-breakpoint-down(sm) { /* max 767 */
						font-size: 18px;
						margin-bottom: 18px;
					}
					@include media-breakpoint-down(xs) { /* max 543 */
						font-size: 18px;
						margin-bottom: 20px;
					}
					@media (max-width:530px) and (min-width:320px) {
						font-size: 14px;
						margin-bottom: 14px;
					}
				}
				.topbanner-button {
					background: $gray-dark;
					display: none;
					border: 2px solid $brand-primary;
					color: $gray-light;
					line-height: normal;
					padding: 7px;
					transition: all 0.3s ease 0s;
					text-transform: uppercase;
					font: 500 14px $font-custom;
					a {
						color: $gray-light;
					}
					@include media-breakpoint-down(md) { /* max 991 */
						font-size: 14px;
						padding: 3px;

					}
					@include media-breakpoint-down(sm) { /* max 767 */


					}
					@include media-breakpoint-down(xs) { /* max 543 */
						padding: 10px;

					}
					@media (max-width:530px) and (min-width:320px) {
						padding: 5px;
						font-size: 12px;
					}
					&:hover {
						background: $brand-primary;
						border: 2px solid $gray-light;
						a {
							color: $gray-dark;
						}
					}
				}
			}
			&:hover{
				.topbanner-detail {
					opacity: 1;
					transform: translateY(0px);
					transform: translateY(0px);
					-moz-transform: translateY(0px);
					-webkit-transform: translateY(0px);
					-khtml-transform: translateY(0px);
					-o-transform: translateY(0px);
				}
			}
		}	
	}
}



