.rvcategorysearch {	  
  margin: auto;
  text-align: center;
  width: 49%;
  @include media-breakpoint-down(md) { /* max 991 */
    width: 54%; 
  }
  @media (max-width:540px) and (min-width:320px) {
    width: 70%;
 }
 #category_search {
  position: relative;
  #searchbox {
   background: $brand-primary;
   border-radius: 25px;
   float: left;
   position: relative;
   width: 100%;
   margin: 10px 0;
   @include media-breakpoint-down(xs) { /* max 543 */
    width: 80%;
  }
  @media (max-width:540px) and (min-width:320px) {
   width:90%;
 }
 .rvsearch-main {
   background: $light-color;
   border-radius: 25px 0 0 25px;
   float: left;
   width: auto;
   .searchboxform-control {
    float: left;
    overflow: hidden;
    position: relative;
    width: 200px;
    @include media-breakpoint-only(md) {
      width: 180px;
    }
    @include media-breakpoint-only(sm) { 
      width: 160px;
    }
    @include media-breakpoint-down(xs) { /* max 543 */
     width: 120px;
   }
   &:before {
     background: #cbcbcb none repeat scroll 0 0;
     content: "";
     height: 30px;
     position: absolute;
     right: 20px;
     top: 5px;
     width: 1px;
   }
   &:after {
    content: "\f107";
    font-family: $font-icon;
    background: $light-color;
    border: 1px solid $gray-light;
    border-radius: 50%;
    height: 15px;
    pointer-events: none;
    position: absolute;
    right: 13px;
    top: 12px;
    width: 15px;
    font-size: 15px;
    line-height: 14px;
    color: $gray-light;         }
    #all_category {
      -moz-appearance: none;
      -webkit-appearance: none;
      -ms-appearance: none;
      -o-appearance: none;
      appearance: none;
      background: $light-color none repeat scroll 0 0;
      border: medium none;
      border-radius: 25px 0 0 25px;
      color: $gray-darker;
      height: 40px;
      line-height: normal;
      overflow: auto;
      padding: 10px 30px 10px 15px;
      width: 100%;
      option {
       padding: 2px;
       color: $gray-darker;
     }
   }
 }
}
}
.btn.button-search {
  transition: all 0.3s ease 0s;
  background: $brand-secondary;
  border: 1px solid $brand-secondary;
  border-radius: 25px;
  color: $brand-primary;
  display: inline-block;
  float: right;
  height: 40px;
  margin: 0;
  padding: 8px 35px 8px 30px;
  text-align: center;
  right: -100px;
  transition: all 0.7s ease 0s;
  position: absolute;
  line-height: 30px;
  font: 500 15px/20px $font-family;
  @include media-breakpoint-down(md) { /* max 991 */
    padding: 8px 30px 8px 15px;
    right: -60px;
  }
  @include media-breakpoint-down(sm) { /* max 767 */
    padding: 9px 30px 10px 15px;
    right: -50px;
  }
  @include media-breakpoint-down(xs) { /* max 543 */
    padding: 9px 30px 10px 11px;
    font: 500 13px/20px $font-family;
    right: -72px;
  }
  @media (max-width:540px) and (min-width:320px) {
    right: -8px;
  }
  &:after {
   background: url(../../assets/img/sprite.png) no-repeat -16px -346px transparent;
   content: "";
   height: 20px;
   right: 0px;
   position: absolute;
   top: 10px;
   width: 20px;
   margin: 0 10px;
 } 
 &:hover {
  border: 1px solid $light-color;
  background: $brand-primary;
  color: $brand-secondary;
  &:after {
    background-position: -74px -346px;
  }
}
span {
  @media (max-width:540px) and (min-width:320px) {
    display: none;
  }
}
}
.input-wrapper {
  overflow: hidden;
  .search_query {
   border: medium none;
   border-radius: 0;
   height: 40px;
   padding: 10px 0px;
   width: 100%;
   @media (max-width:540px) and (min-width:320px) {
    width: 80%;
  }
}
}
#rvsearch {
  background: $light-color none repeat scroll 0 0;
  border: 1px solid $base-border-color;
  display: none;
  position: absolute;
  top: 40px;
  width: 100%;
  z-index: 9;
  .ajax-search-content {
   .items-list {
    padding: 10px;
    .item {
     text-align: left;
     a {
      border: medium none;
      border-radius: 0;
      cursor: pointer;
      display: inline-block;
      margin: 0;
      padding: 5px;
      width: 100%;
      .searchImg {
       border: 1px solid $base-border-color;
       float: left;
       height: auto;
       margin: 0 20px 0 0;
       max-width: 80px;
     }
     .category-name {
       color: $gray;
       display: block;
       font-size: 15px;
       margin: 12px 0 10px;
     }
     .product-name {
       display: block;
       font-size: 16px;
       font-weight: 500;
       margin: 0 0 10px;
     }
     .content_price {
       color: $brand-primary;
       display: block;
       .price {
        color: $gray-darker;
        font-weight: 500;
      }
      .sale-percentage {
        margin: 0 3px;
      }
      .regular-price {
        color: $gray;
        font-weight: 400;
        text-decoration: line-through;
      }
    }
    &:hover {
     background: #e5e5e5 none repeat scroll 0 0;
     .product-name {
      color: $brand-primary;
    }
  }
}
}
}
}
}
}
}


