#product {
  #content {
    position: relative;
    max-width: 470px;
    margin: 0 auto;
    @include media-breakpoint-down(sm) { /* max 767 */
      margin-bottom: 20px;
    }
  }
}
.product-price {
  color: $brand-primary;
  display: inline-block;
}
#product-description-short {
  color: $gray-darker;
}
.product-information {
  font-size: $font-size-lg;
  color: $gray-darker;
}
.input-container {
  position: relative;
}
.input-color, .input-radio {
  position: absolute;
  top: 0;
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.input-color,
.input-radio {
  &:checked + span,
  &:hover + span {
    border: 1px solid $gray-darker;
  }
}
.radio-label {
  @include box-shadow;
  background: white;
  display: inline-block;
  padding: rem(2px) $small-space;
  font-weight: 600;
  border: 2px solid white;
}
.product_title {
  border-bottom: 1px solid #ccc;
  margin: 0 0 15px;
  padding: 0 0 10px;
  font-weight: normal;
  font-size: 20px;
  color: $gray-darker;
}
.product-actions {
  .control-label {
    margin-bottom: 8px;
    display: block;
    width: 100%;
    font-weight: 500;
  }
  .add-to-cart {
    height: 40px;
    padding: 10px 8px;
    line-height: inherit;
    border-color: $button-bkg-hover-color;
    background: $button-bkg-hover-color;
    color: $button-text-hover-color;
    &:hover {
      border-color: $button-bkg-color;
      background: $button-bkg-color;
      color: $button-text-color;
    }
    .material-icons {
      line-height: inherit;
    }
  }
}

.product-add-to-cart {
  display: block;
  margin-bottom: 10px;
  .control-label {
    display: none;
  }
}
.product-quantity { 
  display: block;
  .qty {
    margin-right: 15px;
  }
}
.product-quantity {
  display: block;
  .qty {
    float: left;
    display: inline-flex;
    margin-bottom: .5rem;
  }
  .add {
   float: left;
   display: inline-flex;
   margin-bottom: .5rem;
   > div {
    display: inline-block;
    vertical-align: middle;
    margin: 0 3px;
  }
  .btn-tertiary, .btn-secondary, .btn-primary {
    border: 1px solid $gray-dark-color;
    border-radius: 5px;
    height: 38px;
    line-height: 38px;
    padding: 0;
    position: relative;
    text-align: center;
    width: 36px;
    transition: all 0.5s ease 0s;
    clear: both;
  }
  .wishlist_btn {
    .btn {
      border: 1px solid $brand-primary;
      background-position: -67px -475px;
      background-repeat: no-repeat;
      background-image: url(../../assets/img/sprite.png);
      background-color: $brand-primary;
      &:hover {
        background-color: $brand-secondary;
        border: 1px solid $brand-secondary;
        background-position: -9px -475px;
      }
      span {
        display: none;
      }
      i {
        display: none;
      }
    }
  }
  .product-cart-btn {
    .btn {
      background: $brand-primary;
      border: 1px solid $brand-primary;
      color: $brand-secondary;
      float: left;
      font: 400 14px/20px $font-family;
      padding: 8px 15px 10px 40px;
      position: relative;
      width: 100%;
      z-index: 1;
      border-radius: 5px; 
      &:before {
        content: "";
        height: 30px;
        left: 0;
        position: absolute;
        top: 0;
        width: 40px;
        background: url(../../assets/img/sprite.png) -66px -522px no-repeat;
      }
      &:hover {
        background-color: $brand-secondary;
        border: 1px solid $brand-secondary;
        color: $brand-primary;
        &:before {
          background-position: -8px -522px;
        }
      }
      span {
      }
      i {
        display: none;
      }
    }
  }
  .compare {
    .btn {
      background-position:-68px -569px;
      background-repeat: no-repeat;
      background-image: url(../../assets/img/sprite.png);
      border: 1px solid $brand-primary;
      background-color: $brand-primary;
      &:hover {
        background-color: $brand-secondary;
        border: 1px solid $brand-secondary;
        background-position: -10px -569px;
      }
    }
    span {
      display: none;
    }
    i {
      display: none;
    }
  }
}
#quantity_wanted {
  color: $gray-darker;
  background-color: white;
  height: 40px;
  padding: 0.175rem 0.5rem;
  width: 48px;
  float: left;
  text-align: center;
  z-index: auto;
}
.input-group-btn-vertical {
  float: left;
}
.input-group-btn-vertical {
  .btn {
    padding: 8px 11px;
    i {
      font-size: 16px;
      top: 2px;
      left: 3px;
    }
  }
}
.btn-touchspin {
  height: 21px;
}
}
.product-discounts {
  > .product-discounts-title {
    font-weight: normal;
    font-size: $font-size-sm;
  }
  > .table-product-discounts {
    thead tr th {
      width: 33%;
      padding: $small-space $medium-space;
      background: white;
      border: rem(5px) $gray-light solid;
      text-align: center;
    }
    tbody tr {
      background: $gray-lighter;
      &:nth-of-type(even) {
        background: white;
      }
      td {
        padding: $small-space $medium-space;
        text-align: center;
        border: rem(5px) $gray-light solid;
      }
    }
  }
}
.product-prices {
  font-weight: 400;
  font-size: 15px;
  color: $gray-darker;
  margin-bottom: 15px;
  div {
    display: inline-block;
    vertical-align: middle;
  }

  .tax-shipping-delivery-label {
    font-size: rem(13px);
    color: $gray-dark;
  }
}
.product-discount {
  color: $gray;
  margin-bottom: 0;
  .regular-price {
    text-decoration: line-through;
    font-weight: 500;
    margin-right: .625rem;
    font-size: 18px;
  }
}
.has-discount {
  &.product-price, p {
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 0;
  }
  .discount {
    background: $gray-light;
    color: $gray-darker;
    padding: 7px 11px;
    font-size: 13px;
    line-height: 15px;
    font-weight: 400;
    margin: 0 8px;
    text-transform: uppercase;
    display: inline-block;
    vertical-align: middle;
  }
  .current-price > span {
    display: inline-block;
    vertical-align: middle;
  }
}
.product-unit-price {
  font-size: $font-size-xs;
  margin-bottom: 0;
}
.tabs {
 margin-bottom: 50px;
  .nav-tabs {
    border-bottom: none;
    margin-bottom: 30px;
    text-align: center;
    .nav-item {
      display: inline-block;
      vertical-align: middle;
      float: none;
      margin-bottom: -.125rem;
      text-transform: uppercase;
      margin-left: 10px;
      @media (max-width:530px) and (min-width:320px) {
        width: 100%;
        margin: 15px 0;
      }
      a {
        color: $brand-primary;
        padding-left: 1px;
        padding-right: 1px;
        padding: 8px 12px;
        border: 1px solid $gray-light;
        border-top-color: $gray-light;
        border-right-color: $gray-light;
        border-bottom-color: $gray-light;
        border-left-color: $gray-light;
        background: $light-color;
        letter-spacing: 1px;
        text-decoration: none;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        font-size: 14px;
        font-weight: 500;
        border-radius: 4px;
        position: relative;
        &:before {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          width: 0;
          height: 0;
          bottom: -9px;
          margin: 0 auto;
          border-style: solid;
          border-width: 8px 10px 0 10px;
          border-color: transparent transparent transparent transparent;
          -webkit-transition: all 0.5s;
          -moz-transition: all 0.5s;
          -o-transition: all 0.5s;
          transition: all 0.5s;
        }
        &.active {
          border-color: $brand-primary;
          background: $brand-primary;
          color: $brand-secondary;
          &:before {
            border-color: $brand-primary transparent transparent transparent;
          }
        }
        &:hover, &:focus, &:active {
          border-color: $brand-primary;
          background: $brand-primary;
          color: $brand-secondary;
          &:before {
            border-color: $brand-primary transparent transparent transparent;
          }
        }
      }
      &:first-child {
        margin-left: 0;
    }
    }
  }
  #tab-content {
    padding: 15px;
    border: 1px solid #e4e4e4;
    text-align: center;
    background-color: $light-color;
    #description {
      .product-description {
        p {
          margin: 0;
          span {
            line-height: 28px;
            margin-bottom: 0;
            letter-spacing: .8px;
            font-size: 14px !important;
          }
        }
      }
    }
  }
}
.product-cover {
  margin-bottom: $medium-space;
  position: relative;
  img {
    box-shadow: 0 0 0px 2px #dedede;
    background: white;
  }
  .layer {
    bottom: 5px;
    cursor: pointer;
    position: absolute;
    right: 5px;
    z-index: 998;
    .zoom-in {
      font-size: rem(18px);
    }
  }
}
#product-modal {
  .modal-content {
    background: transparent;
    border: none;
    padding: 0;
    .modal-body {
      .product-cover-modal {
        background: white;
        max-width: 100%;
      }
      .image-caption {
        background: white;
        width: 100%;
        padding: rem(10px) rem(20px);
        border-top: $gray-light 1px solid;
        p {
          margin-bottom: 0;
        }
      }
      .thumbnails {
        position: relative;
        .owl-nav {
          > div  {
            color: $light-color;
            &:hover {
              color: $link-hover-color;
            }
          }
        }
      }
    }
  }
}
#blockcart-modal {
  color: $gray-darker;
  .modal-header {
  }
  .modal-body {
    padding: rem(50px) rem(30px);
    @include media-breakpoint-down(md) {
      padding: rem(30px);
    }
    @include media-breakpoint-down(sm) {
      padding: rem(16px);
      text-align: center;
    }
    .divide-right span {
      display: inline-block;
      margin-bottom: rem(5px);
    }
  }
  .modal-dialog {
    max-width: 1140px;
    width: 100%;
    @include media-breakpoint-down(md) {
      width: calc(100% - 30px);
    }
  }
  .product-image {
    max-width: 100%;
  }
  .modal-title {
    font-weight: 600;
    font-size: rem(18px);
    i {
      font-weight: 400;
      margin: 0 10px;
    }
  }
  .product-name {
    color: $brand-primary;
    font-size: rem(18px);
  }
  .cart-products-count {
    font-size: rem(18px);
    font-weight: bold;
  }
  .cart-content {
    padding-left: $extra-large-space;
    @include media-breakpoint-down(md) {
      padding-left: 0;
    }
    button {
      margin-right: rem(5px);
    }
  }
  .divide-right {
    border-right: $base-border-color 1px solid;
    @include media-breakpoint-down(sm) {
      border-right: medium none;
      border-bottom: 1px solid $base-border-color;
      margin-bottom: 20px;
    }
  }
}
.product-images {
  li.thumb-container {
    .thumb {
      cursor: pointer;
      border: 1px solid $base-border-color;
      border-radius: 5px; 
      &.selected,
      &:hover {
        border: $brand-primary 1px solid;
      }
    }
  }
}
.js-qv-mask, .js-modal-mask {
  margin: 0 30px;
  ul {
    margin-bottom: 0;
  }
  li {
    padding: 10px;
  }
  .owl-nav {
    left: 0;
    margin: 0;
    position: absolute;
    right: 0;
    top: 50%;
    z-index: 1;
    > div  {
      width:30px;
      height:30px;
      font-size: 28px;
      padding: 0px;
      position: absolute;
      margin: -15px 0 0;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      transition: all 0.3s ease 0s;
      text-align: center;
      &:hover {
        color: $link-hover-color;
      }
    }
    .owl-prev {
      left: -30px;
      right:auto;
      i {
        &:before {
          content: "\f104";
        }
      }
    }
    .owl-next {
      right: -30px;
      left: auto;
      i {
        &:before {
          content: "\f105";
        }
      }
    }
  }
}

#product-availability {
  display: inline-block;
  font-weight: 600;
  margin: 10px 0 0 15px;
  vertical-align: middle;
  i {
    line-height: inherit;
    line-height: 22px;
  }
  .product-available {
    color: $brand-success;
  }
  .product-unavailable {
    color: $brand-warning;
  }
  .product-last-items {
    color: $brand-warning;
  }
}
#product-details {
  .product-manufacturer {
    margin-bottom: 20px;
    img {
      border: medium none;
    }
  }
  .label {
    font-size: $font-size-base;
    color: $gray-darker;
    font-weight: bold;
  }
}
.product-features {
  margin-top: $medium-space;
  > dl.data-sheet {
    display: inline-block;
    width: 100%;
    dd.value,
    dt.name {
      font-weight: normal;
      float: left;
      width: 50%;
      background: $gray-light;
      padding: $small-space;
      margin: 0;
      min-height: rem(40px);
      word-break: break-all;
      &:nth-of-type(even) {
        background: $gray-lighter;
      }
    }
    dt.name {
      text-transform: capitalize;
      font-weight: bold;
      color: $base-heading-color;
      clear: left;
    }
  }
}
.product-variants {
  > .product-variants-item {
    margin-bottom: 20px;
    select {
      border: 1px solid $gray-light;
      width: auto;
      padding-right: 1.875rem;
    }
    ul li {
      margin-right: $small-space;
      display: inline-block;
      vertical-align: middle;
      label {
        margin-bottom: 0; 
      }
    }
    .color {
      margin-left: 0;
      margin-top: 0;
    }
    ul {
      margin: 0;
    }
  }
}
.product-flags {
  margin: 0;
}
li.product-flag {
  position: absolute;
  color: $base-heading-color;
  font-size: rem(12px);
  font-weight: 600;
  text-transform: uppercase;
  &.discount, &.on-sale{
    top: 10px;
    left: 10px;
  }
  &.new {
    top: 10px;
    right: 10px;
  }
  &.online-only {
    left: 10px;
    bottom: 10px;
    &::before{
      content:"\E30A";
      font-family: 'Material Icons';
      vertical-align: -2px;
      margin: rem(5px);
      font-size: rem(12px);
    }
  }
  &.pack {
    color: $brand-primary;
    right: 10px;
    top: 30px;
  }
}

.product-customization {
  @include box-shadow;
  margin: $extra-large-space 0;
  .product-customization-item {
    margin: $medium-space 0;
  }
  .product-message {
    background: $gray-light;
    border: none;
    width: 100%;
    height: rem(50px);
    resize: none;
    padding: rem(10px);
    @include placeholder {
      color: $gray;
    }
  }
  .file-input {
    width: 100%;
    opacity: 0;
    left: 0;
    z-index: 1;
    cursor: pointer;
    height: rem(42px);
    overflow: hidden;
    position: absolute;
  }
  .custom-file {
    position: relative;
    background: $gray-light;
    width: 100%;
    height: rem(42px);
    line-height: rem(42px);
    text-indent: rem(10px);
    display: block;
    color: $gray;
    margin-top: $medium-space;
    button {
      z-index: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  small {
    color: $gray;
  }
}
.product-pack {
	margin-top: $extra-large-space;
	.pack-product-container {
		padding: rem(15px);
		@include media-breakpoint-down(xs) { /* max 543 */
			text-align: center;
		}
		.pack-product-name {
			font-size: rem(14px);
			color: $gray;
		}
		.pack-product-quantity {
		}
		.thumb-mask, 
		.pack-product-name,
		.pack-product-price {
			@include media-breakpoint-down(xs) { /* max 543 */
				margin-bottom: rem(10px);
			}
		}
	}
}
.product-refresh {
  margin-top: $medium-space;
}
.social-sharing {
  margin-bottom: 15px;
  span {
    display: block;
    margin-bottom: .375rem;
    font-weight: 500;
  }
  ul {
    margin-bottom: 0;
  }
  li {
    height: 35px;
    width: 60px;
    display: inline-block;
    background-color: #FFF;
    cursor: pointer;
    margin-right: 4px;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    a {
      display: block;
      width: 100%;
      height: 100%;
      white-space: nowrap;
      text-align: center;
      line-height: 35px;
      text-indent: unset;
      overflow: hidden;
      border-radius: 5px;
      span {
        display: none;
      }
      &:before {
       display: inline-block;
       vertical-align: middle;
       font-family: FontAwesome;
       font-weight: 400;
       position: relative;
       font-size: 18px;
       line-height: 33px;
       color: $light-color;
       -webkit-font-smoothing: antialiased;
       -moz-osx-font-smoothing: grayscale;
     }
     &:hover {
      color: $link-hover-color;
    }
  }
  &.facebook {
    a {
      background: #3b5999;
      &:before {
        content: "\f09a";
      }
      &:hover{
        background: $brand-primary;
        color:#3b5999;
      }
    }
  }
  &.twitter {
    a {
      background: #00aced;
      &:before {
        content: "\f099";
      }
      &:hover{
        background: $brand-primary;
        color:#00aced;  
      }
    }
  }
  &.googleplus {
    a {
      background: #dc473b;
      &:before {
        content: "\f0d5";
      }
      &:hover{
        background: $brand-primary;
        color:#dc473b;  
      }
    }
  }
  &.pinterest {
    a {
      background: #cb2027;
      &:before {
        content: "\f231";
      }
      &:hover{
        background: $brand-primary;
        color:#cb2027;  
      }
    }
  }
}
}




.payment_logo_block {
  padding: 0;
  margin-bottom: 25px;
  span {
    display: block;
    margin-bottom: .375rem;
    font-weight: 500;
  }
  img {
    margin: 1px 5px 1px 0;
  }
}

.products-selection {
  .title {
    color: $gray-dark;
  }
}

#blockcart-modal .cart-content {
  .btn {
    margin-bottom: $small-space;
  }
}

#description {
  .product-description {
    color: $gray-dark;
    line-height: 22px;
    ul, ol, dl {
      padding-left: 40px;
      list-style: inherit;
    }
  }
}

.product-additional-info {
  display: block;
  overflow: hidden;
  .payment_logo_block { 
    width: auto;
  }
}




