.rvtheme-control {
    position: fixed;
    z-index: 1001;
    top: 110px;
    right:-427px; 
    transition: all 0.7s ease-in-out 0s;
    @include media-breakpoint-down(sm) { /* max 767 */
        display: none;
    }
    &.active {
        right: 0;
        transition: all 0.7s ease-in-out 0s;
    }
    .rvcontrol-icon {
        width: 45px;
        height: 45px;
        background-color: $brand-secondary;
        line-height: 43px;
        text-align: center;
        float: left;
        position: absolute;
        left: -45px;
        cursor: pointer;
        box-shadow: -1px 0px 5px 0px rgba(0, 0, 0, 0.30);
        border-radius: 5px 0 0 5px;
        &:hover {
         background-color: $brand-primary;
         color: $light-color;
     }
     i {
        font-size: 28px;
        color: $brand-primary;
        animation: 4s linear 0s normal none infinite running spinAround;
    }
    &:hover {
        i {
            color: $light-color;
        }
    }
}
.rvcontrol-wrapper {
    width: 427px;
    height: auto;
    padding: 15px 30px;
    background-color: $brand-primary;
    color: $light-color;
    .rvcontrol-reset {
        float: right;
        background-color: $brand-secondary;
        border: none;
        color: $brand-primary;
        padding: 5px 15px;
        text-transform: uppercase;
        cursor: pointer;
        border-radius: 5px;
        &:focus {
            outline: none;
        }
    }
    table {
        width: 100%;
        border-collapse: unset;
        td {
            padding: 10px 0;
            border: none !important;
        }
    }
    .rvselect-theme {
        select {
            padding: 2px 0;
            width: 165px;
            border: 1px solid $gray;
            border-radius: 5px;
            &:focus {
                outline: none;
            }
            option {
                border: 1px solid $gray;
            }
        }
        .rvselect-theme-name {
            margin-right: 10px;
            font-family: $font-family;
            font-size: 16px;
        }
    }
    .rvall-theme-content {
        margin-bottom: 15px;
        border: none;
    }
    .rvtheme-color-one {
        margin-bottom: 10px;
        overflow: hidden;
    }
    .rvtheme-color-box {
        float: left;
        input {
            width: 165px;
            border:none;
        }
    }
    .rvtheme-layout-name {
        margin-right: 10px;
        font-family: $font-family;
        font-size: 16px;
    }
    .rvtheme-background-pattern-name {
        margin-right: 10px;
        font-family: $font-family;
        font-size: 16px;
    }
    .rvbgcolor-theme-name {
        margin-right: 10px;
        font-family: $font-family;
        font-size: 16px;
    }
    .rvtheme-right-sticky-name {
     margin-right: 10px;
     font-family: $font-family;
     font-size: 16px;
 }
 .rvtheme-option {
    border: none;
    margin: 0;
    color: $light-color;
    .btn-primary {
        background-color: $brand-secondary;
        text-align: center;
        border: none;
        margin: 0;
        text-transform: uppercase;
        border-radius: 5px;
        color: $brand-primary;
        &.toggle-on {
            line-height: 35px;
            padding: 0 30px 0 0;
        }
    }
    .btn-default {
        background-color: $gray;
        color: $light-color;
        text-align: center;
        margin: 0;
        border: none;
        border-radius: 5px; 
        text-transform: uppercase;
        &.toggle-off {
            line-height: 35px;
            padding: 0 0 0 30px;
        }
    }
    .toggle-handle {
        background-color: $light-color;
        padding: 0px 30px;
    }
}
.rvtheme-background-patten { 
    p.notice {
        font-size: 10px;
        display: block;
        float: left;
        color: $light-color;
        margin: 0;
    }
    .rvtheme-all-pattern-wrapper {
        width: 185px;
        .rvtheme-all-pattern {
            width: 25px;
            height: 25px;
            background-color: $light-color;
            float: left;
            margin: 0 5px 5px 0;
            cursor: pointer;
            .rvtheme-pattern-image {
                width: 25px;
                height: 25px;
                border:1px solid $gray-light;
                &:hover {
                    border: 2px solid $gray;
                }
            }
        }
    }
}
.rvtheme-bgcolor-box {
    input {
        width: 175px;
        border: 1PX solid $gray;
    }
    .minicolors-position-left {
        .minicolors-panel {
            border: 1px solid $gray;
        }
    }
}
}
}

.rvright-sticky {
    right: 0;
    position: fixed;
    top: 0;
    white-space: nowrap;
    width: auto;
    z-index: 1000;
    margin: auto;
    bottom: 0;
    max-height: 200px;
    .sticky-content {
        position: relative;
        min-width: 40px;
        width: auto;
        .sticky-inner {
            position: absolute;
            .sticky-cart {
                background-color: $light-color;
                box-shadow: 0 0 8px 1px rgba(184,184,184,0.8);
                padding: 0 10px;
                height: 28px;
                text-transform: uppercase;
                border-radius: 15px 0 0 15px;
                display: block;
                -webkit-transition: all 0.2s linear;
                -moz-transition: all 0.2s ease;
                -ms-transition: all 0.2s ease;
                -o-transition: all 0.2s ease;
                transition: all 0.2s ease;
                margin: 10px 0;
                overflow: hidden;
                a {
                    width: 100%;
                    display: inline-block;
                    padding: 0 0 0px 30px;
                    font-size: 14px;
                    position: relative;
                    &:after {
                     background: url(../../assets/img/sticky.png) no-repeat -59px -48px transparent;
                     content: "";
                     height: 28px;
                     left: 0px;
                     position: absolute;
                     top: 0;
                     width: 28px;
                 }
                 i {
                    display: none;
                }
                span {
                    color: $light-color;
                    font-size: 12px;
                    float: left;
                    line-height: 30px;
                    opacity: 1;
                    -webkit-transition: all 0.2s linear;
                    -moz-transition: all 0.2s ease;
                    -ms-transition: all 0.2s ease;
                    -o-transition: all 0.2s ease;
                    transition: all 0.2s ease;
                }
            }
            &:hover {
                margin-left: calc(-100% + 30px);
                background: $brand-primary;
                a {
                    &:after {
                        background-position: -7px -48px;
                    }

                    span {
                        opacity: 1;
                    }
                }
            }
        }
        .sticky-wishlist {
            background-color: $light-color;
            box-shadow: 0 0 8px 1px rgba(184,184,184,0.8);
            padding: 0 10px;
            height: 28px;
            text-transform: uppercase;
            border-radius: 15px 0 0 15px;
            display: block;
            -webkit-transition: all 0.2s linear;
            -moz-transition: all 0.2s ease;
            -ms-transition: all 0.2s ease;
            -o-transition: all 0.2s ease;
            transition: all 0.2s ease;
            margin: 10px 0;
            overflow: hidden;
            a {
                width: 100%;
                display: inline-block;
                padding: 0 0 0px 30px;
                font-size: 14px;
                position: relative;
                &:after {
                 background: url(../../assets/img/sticky.png) no-repeat -58px -94px transparent;
                 content: "";
                 height: 28px;
                 left: 0px;
                 position: absolute;
                 top: 0;
                 width: 28px;
             }
             i {
                display: none;
            }
            span {
                color: $light-color;
                font-size: 12px;
                float: left;
                line-height: 30px;
                opacity: 1;
                -webkit-transition: all 0.2s linear;
                -moz-transition: all 0.2s ease;
                -ms-transition: all 0.2s ease;
                -o-transition: all 0.2s ease;
                transition: all 0.2s ease;
            }
        }
        &:hover {
            margin-left: calc(-100% + 30px);
            background: $brand-primary;
            a {
                &:after {
                 background-position: -8px -94px;
             }

             span {
                opacity: 1;
            }
        }
    }
}
