#rvproductstab {
	position: relative;
	margin-bottom: 10px;
	padding: 0;
}

#rvcategoryslider {
	position: relative;
	margin-bottom: rem(50px);
	.categoryimage {
		margin-bottom: rem(20px);
		img {
			max-width: 100%;
		}
	}
}

.products_block .products_block_inner {
	.rv-titletab {
		overflow: hidden;
		margin-bottom: 30px;
		background-color: $light-color;
		padding: 10px 15px;
		border-radius: 10px;
		.tab_title {
			float: left;
			padding: 0;
			margin: 0;
			font: 500 16px/16px $font-custom;
			color: $brand-primary;
			padding: 10px 0 10px;
			text-transform: uppercase;
			position: relative;
			a {
				color:$brand-primary;
			}
			&:before {
				border-bottom: 2px solid $brand-secondary;
				bottom: -10px;
				content: "";
				left: 0;
				position: absolute;
				width: 110px;
				display: inline-block;
			}
		}
		.nav-tabs {
			border: medium none;
			float: right;
			padding: 10px 90px 0 0;
			text-align: center;
			@include media-breakpoint-down(xs) { /* max 543 */
				display: none;
			}
			> li {
				display: inline-block;
				float: none;
				margin: 0;
				@include media-breakpoint-down(xs) { /* max 543 */
					margin: 0;
				}
				@media only screen and (min-width: 320px) and (max-width :480px) {
					width : 100%;
					margin: 0 0 3px;
					text-align: left;
				}
				a {
					font: 500 16px/16px $font-custom;
					margin: 0;
					border: medium none;
					padding: 0 20px 10px;
					display: inline-block;
					text-transform: uppercase;
					background-color: transparent;
					vertical-align: middle;
					position: relative;
					color: $brand-primary;
					overflow: hidden;
					transition-duration: 0.3s;
					transition: all 0.3s ease-in-out 0s;
					-webkit-transition: all 0.3s ease-in-out 0s;
					-moz-transition: all 0.3s ease-in-out 0s;
					-ms-transition: all 0.3s ease-in-out 0s;
					-o-transition: all 0.3s ease-in-out 0s;
					@include media-breakpoint-down(xs) { /* max 543 */
						font: 500 15px/15px $font-custom;
						margin: 12px 0;
						padding: 0 10px 0px;
					}
					&:after {
						content: "|";
						position: absolute;
						right: 0;
						@include media-breakpoint-down(xs) { /* max 543 */
							display: none;
						}
					}
					&.active {
						color: $brand-secondary;
						background-color: transparent;
					}
					&:hover, &:focus, &:active {
						color: $brand-secondary;
						background-color: transparent;
					}
				}
				&:last-child {
					a {
						&:after {
							display: none;
						}
					}
				}
			}
		}
	}
}


