.forgotten-password {
	padding: 4px;
	.form-fields {
		.center-email-fields {
			.input-wrapper {
				overflow: hidden;
				margin-bottom: 10px;
			}
			input[type=text] {
				width: 100%;
				min-width: rem(240px);
				color: $gray-darker;
				text-align: left;
				padding: rem(10px);
				background: $gray-light;
				border: none;
			}
			button[type=submit] {
			}
			input, button {
				vertical-align: middle;
			}
		}
		label.required {
			text-align: left;
		}
	}
}


