.block_newsletter {
 float: left;
 margin-top: 0;
 padding: 0;
 width: 100%;
 background-color: $light-color;
 margin-bottom: 30px;
 border-radius: 10px;
 padding: 20px 0;
 text-align: center;
 .newsletter_content {
  margin: 20px 0 0 0;
  padding: 0 15px;
  .newsletter_content_inner {
  }
  p.newsletter_text {
    font-size: 12px;
    opacity: 0.5;
    padding: 0 10px;
    margin-bottom: 25px;
  }
}
.title {
  text-align: center;
  font-size: 22px;
  text-transform: capitalize;
  letter-spacing: 1px;
  font-family: $font-family;
  font-weight: 600;
  margin-bottom: 20px;
  margin-top: 0px;
  color: $brand-primary;
  padding: 120px 0 0;
  position: relative;
  &:after {
   content: "";
   height: 100px;
   left: 0px;
   position: absolute;
   right: 0;
   top: 0px;
   width: auto;
   background: url(../../assets/img/newsletter.png) no-repeat transparent center center;
 }
}
hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top-width: 0px;
  border-top-style: none;
  border-top-color: currentcolor;
  border-top: 1px solid $gray-light;
  width: 60px;
}
form {
  .newsletter_condition {
    display: none;
  }
  .newsletter_form_inner {
    overflow: hidden;
    position: relative;
    border-radius: 5px; 
  }
  .input-wrapper {
    overflow: hidden;
    margin-bottom: 15px;
  }
  input[type=text] {
    background: $light-color;
    border: 1px solid $base-border-color;
    color: $brand-primary;
    min-width: 9.375rem;
    text-align: left;
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    background-color: $light-color;
    border-radius: 25px;
    @include media-breakpoint-down(xs) { /* max 543 */
    }
  }
  button[type=submit] {
    color: $brand-primary;
    border: none;
    background: $brand-secondary;
    border-radius: 25px;
    font-size: 13px;
    text-transform: uppercase;
    font-family: $font-family;
    padding: 8px 30px;
    -webkit-transition: all 0.7s ease 0s;
    -moz-transition: all 0.7s ease 0s;
    -ms-transition: all 0.7s ease 0s;
    -o-transition: all 0.7s ease 0s;
    transition: all 0.7s ease 0s;
    transition-property: transform;
    transition-timing-function: ease-out;
    margin-top: 5px;
    position: relative;
    display: block;
    width: 100%;
    &:before {
      background: $brand-primary;
      bottom: 0;
      content: "";
      left: 0;
      position: absolute;
      right: 0;
      top: 0;
      transform: scaleX(0);
      transform-origin: 50% 50% 0;
      -webkit-transition: all 0.7s ease 0s;
      -moz-transition: all 0.7s ease 0s;
      -ms-transition: all 0.7s ease 0s;
      -o-transition: all 0.7s ease 0s;
      transition: all 0.7s ease 0s;
      transition-property: transform;
      transition-timing-function: ease-out;
      z-index: -1;
      border-radius: 25px; 
    }
    &:after {
      content: "\f003";
      font-family: FontAwesome;
      font-size: 20px;
      line-height: 22px;
      display: none;
    }
    &:hover {
      color: $light-color;
      border: none;
      border-radius: 25px;
      -webkit-transition: all 0.7s ease 0s;
      -moz-transition: all 0.7s ease 0s;
      -ms-transition: all 0.7s ease 0s;
      -o-transition: all 0.7s ease 0s;
      transition: all 0.7s ease 0s;
      transition-property: transform;
      transition-timing-function: ease-out;
      z-index: 1;
      transform: scaleX(1);
      transition-duration: 0.3s;
      transition-duration: all 0.3s ease 0s;
      &:before {
        transform: scaleX(1);
        transition-duration: 0.3s;
        transition-duration: all 0.3s ease 0s;
        border-radius: 25px;
        -webkit-transition: all 0.7s ease 0s;
        -moz-transition: all 0.7s ease 0s;
        -ms-transition: all 0.7s ease 0s;
        -o-transition: all 0.7s ease 0s;
        transition: all 0.7s ease 0s;
        transition-property: transform;
        transition-timing-function: ease-out;
      }
    }
    span {

    }
  }
  button {
    height: 34px;
    vertical-align: middle;
  }
  input {
    height: 34px;
    vertical-align: middle;
  }
}
}

.block-contact {
  float: right;
  @include media-breakpoint-down(md) { /* max 991 */
    float: none;
  }
  .block {
    margin: 0 0 5px;
    overflow: hidden;
    @include media-breakpoint-up(lg) { /* min 992 */
      margin: 0 0 20px;
    }
    .icon {
      float: left;
      margin: 0 8px 0 0;
      text-align: center;
      width: 20px;
      color: $light-color;
      i {
        font-size: 20px;
        line-height: 22px;
        color: $light-color;
      }
    }
    .data {
      line-height: 24px;
      overflow: hidden;
      font-size: 16px;
      color: $light-color;
      font-weight: 400;
      &.email{
      }
      a {
        font-weight: 400;
        transition: all 0.5s ease 0s;
        &:hover {
          transition: all 0.5s ease 0s;
          margin-left: 5px;
        }
      }
    }
  }
}


.account-list a {
  color: $gray;
  &:hover {
    color: $brand-primary;
  }
}

#footer {
  .footer-block {
    @include media-breakpoint-up(lg) { /* max 992 */
      padding: 0 0 0 80px;
    }
  }
  .block-social {
    overflow: hidden;
    @include media-breakpoint-down(md) { /* max 991 */
      float: none;
    }
    ul {
      margin: 0;
      li {
        display: block;
        a {
          font-size: 16px;
          width: 30px;
          height: auto;
          padding: 0;
          background: transparent;
          color: $light-color;
          -webkit-transition: all 0.5s ease 0s;
          -moz-transition: all 0.5s ease 0s;
          -ms-transition: all 0.5s ease 0s;
          -o-transition: all 0.5s ease 0s;
          transition: all 0.5s ease 0s;
          border-radius: 5px; 
          border: none;
          &:after {
            display: none;
          }
          &:before {
            display: inline-block;
            font-family: FontAwesome;
            font-weight: 400;
            position: relative;
            margin: 0 5px 0 0;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
          }
          &:hover {
            border: none;
            color: $brand-secondary; 
          }
        }
        &.facebook {
          a {
            &:before {
              content: "\f09a";
            }
            &:hover{
             color: $brand-secondary; 
           }
         }
       }
       &.twitter {
        a {
          &:before {
            content: "\f099";
          }
          &:hover{
            color: $brand-secondary; 
          }
        }
      }
      &.rss {
        a {
          &:before {
            content: "\f09e";
          }
          &:hover{
           color: $brand-secondary;   
         }
       }
     }
     &.youtube {
      a {
        &:before {
          content: "\f16a";
        }
        &:hover{
          color: $brand-secondary;  
        }
      }
    }
    &.googleplus {
      a {
        &:before {
          content: "\f0d5";
        }
        &:hover{
         color: $brand-secondary;   
       }
     }
   }
   &.pinterest {
    a {
      &:before {
        content: "\f231";
      }
      &:hover{
       color: $brand-secondary;   
     }
   }
 }
 &.vimeo {
  a {
    &:before {
      content: "\f27d";
    }
    &:hover{
      color: $brand-secondary;  
    }
  }
}
&.instagram {
  a {
    &:before {
      content: "\f16d";
    }
    &:hover{
      color: $brand-secondary; 
    }
  }
}
}
}
}
}

.footer-container {
  padding-top: 30px;
  padding-bottom: 10px;
  overflow: hidden;
  @include media-breakpoint-up(lg) { /* min 992 */
    padding-top: 50px;
  } 
  h4.title_block {
    font-size: 22px;
    line-height: 20px;
    text-transform: uppercase;
    color: $light-color;
    margin: 0 0 rem(20px) 0;
    cursor: pointer;
    transition: all 0.5s ease 0s;
    font-weight: 500;
    @media (min-width: 992px) and (max-width: 1366px) {
      font-size: 18px;
    }
    @include media-breakpoint-down(md) { /* max 991 */
      font-size: 18px;
    }
    @include media-breakpoint-down(md) { /* max 991 */
      position: relative;
      cursor:pointer;
      margin: 0;
      padding: 15px 0;
      &:after {
        content: "\f107";
        display: block;
        font-family: $font-icon;
        font-size: 20px;
        vertical-align: middle;
        position: absolute;
        right: 0;
        top: 10px;
      }
      &.active:after {
        content: "\f106";
      }
    }
    a {
      color: $light-color;
      transition: all 0.5s ease 0s;
      font-size: 22px;
      line-height: 20px;
      @include media-breakpoint-down(md) { /* max 991 */
        font-size: 18px;
      }
      @media (min-width: 992px) and (max-width: 1366px) {
        font-size: 18px;
      }
    }
    &:hover {
      color: $brand-secondary;
      a {
        color: $light-color;
      }
    }
  }
  ul {
    margin-bottom: 0;
  }
  li {
    margin-bottom: 5px;
    @include media-breakpoint-up(lg) { /* min 992 */
      margin-bottom: 15px;
    }
    a {
      font-size: 16px;
      line-height: 16px;
      font-family: $font-family;
      transition: all 0.5s ease 0s;
      &:after {
        content: "\E5CC";
        font-family: "Material Icons";
        font-size: 20px;
        float: left;
        color: $light-color
      }
      &:hover {
        transition: all 0.5s ease 0s;
        margin-left: 5px;
        &:after {
          color: $brand-secondary;
        }
      }
    } 
  }
}
.footer-container-after {
  padding-bottom: 15px;
  margin-top: 20px;
  .footer-container-after-inner {
    border-top: 1px solid rgba(255,255,255,.2);
    padding: 10px 0;
    overflow: hidden;
    &.cookie-fixed {
      margin: 0px 0 40px;
    }
  }
  .copyright {
    float: left;
    line-height: 25px;
    font-size: 15px;
    @include media-breakpoint-down(md) { /* max 991 */
      width: 100%;
      text-align: center;
    }
  }
  .payment_logo_block {
    float: right;
    text-align: right;
    margin-bottom: 0;
    padding: 0 15px;
    span {
      display: none;
    }
    @include media-breakpoint-down(md) { /* max 991 */
      width: 100%;
      margin-bottom: 10px;
      text-align: center;
    }
    img {
      cursor: pointer;
      margin: 0 1px;
      transition: all 800ms ease 0s;
      &:hover {
       transform: rotateY(360deg);
       transition: all 800ms ease 0s;
     }
   }
 }
}
.linkblock {

}

.cookie-notice {
  &.fixed {
    animation: 1s ease 0s normal both 1 running fadeInUp;
    left: 0;
    margin: 0;
    position: fixed;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 998;
    background: #1a2533;
    padding: 10px 0; 
  }
  .cookie-content {
    text-align: center;
    span {
     color:$light-color;
     font-family: $font-family;
     font-size: 14px;
     line-height: 20px;
     @include media-breakpoint-down(xs) { /* max 543 */
      font-size: 12px; 
    }
    a {
      strong {
        color: $brand-secondary;
        transition: all 0.5s;
      }
      &:hover {
        strong {
          color: $light-color;
        }
      }
    }
  }
  .btn {
    border: 1px solid $brand-secondary;
    background-color: $brand-secondary;
    color: $brand-primary;
    transition: all 0.5s;
    margin-left: 5px; 
    padding: 2px 12px;
    border-radius: 25px;
    span {
      color: $brand-primary;
    }
    &:hover {
      border: 1px solid $light-color;
      background-color: $light-color;
      color: $brand-primary;
      span {
        color: $brand-primary;
      }
    }
  }
}
}

.rvapp-block {
  text-align: center;
  padding: 15px;
  @include media-breakpoint-down(md) { /* max 991 */  
    text-align: left;
    padding: 0 15px;
  }

  .title_block {
    @include media-breakpoint-up(lg) { /* min 992 */  
      display: none;
    }
  }
  ul {
    margin: 0;
    li { 
      display: inline-block;
      vertical-align: middle;
      margin: 0 5px;
      @media (max-width:530px) {
        display: block;
        margin: 5px 0;
      }
      a {
        margin: 0;
        &:after {
          display: none;
        }
      }
      &:hover {
        a {
          margin: 0;
        }
      }
    }
  }
}



