.search-widget {
	float: right;
    position: relative;
    z-index: 7;
    @include media-breakpoint-down(md) { /* max 991 */
		padding: 10px 0;
	}
	.search-widget-inner {
		position: relative;
	}
	.searchtoggle {
		cursor: pointer;
		position: relative;
		height: rem(40px);
		width: rem(40px);
		display: none;
		background: url(../../assets/img/sprite.png) no-repeat -15px -71px transparent;
		@include media-breakpoint-down(md) { /* max 991 */
			display: block;
			background-position: -170px -172px;
		}
		&.active {
			@include media-breakpoint-down(md) { /* max 991 */
				background-position:  -171px -219px;
			}
		}
	}
	form {
		position: relative;
		display: block;
		right: 0;
		@include media-breakpoint-down(md) { /* max 991 */
			top: 50px;
			display: none;
			position: absolute;
		}
		input[type=text] {
			&:focus {
				outline: none;
				color: $gray-darker;
			}
			@include media-breakpoint-up(lg) { /* min 992 */
				border-left:none;
				border-top: none;
				border-right: none;
				border-bottom: 2px solid $gray-darker;
				width: 230px;
				background: transparent;
			}
			border: 1px solid $base-border-color;
			padding: 10px 40px 10px 10px;
			width: 355px;
			background: $light-color;
			color: $gray-darker;
			@include media-breakpoint-down(xs) { /* max 767 */
				width: 240px;
			}
			@include placeholder {
				color: rgba($gray-darker, 0.5);
				text-indent: rem(10px);
				opacity: 1;
			}
		}
		button[type=submit] {
			position: absolute;
			background: none;
			border: none;
			right: rem(0px);
			color: $gray-darker;;
			padding: 0;
			height: 100%;
			width: rem(40px);
			bottom: 0;
			cursor: pointer;
			&:hover {
				color: $brand-primary;
			}
			i {
				font-size: 16px;
			}
		}
	}
	&:hover {
		.searchtoggle {
			@include media-breakpoint-down(md) { /* max 991 */
				background-position:  -10px -172px;
			}
			&.active {
				@include media-breakpoint-down(md) { /* max 991 */
					background-position:  -11px -219px;
				}
			}
		}
	}
}

.ui-widget.ui-widget-content {
	border-color: $base-border-color;
	border-radius: 0;
	padding: 5px;
	max-width: 360px;
	z-index: 999;
	.ui-menu-item {
		a {
			padding: 5px;
			display: inline-block;
			width: 100%;
			border: medium none;
			border-radius: 0;
			margin: 0;
			cursor: pointer;
			.searchImg {
				float: left;
				height: auto;
				margin: 0 15px 0 0;
				max-width: 80px;
			}
			.category {
				color: #808080;
				display: block;
				font-size: 13px;
			}
			.product {
				display: block;
			}
			.price {
				color: $brand-primary;
				display: block;
			}
			&.ui-state-focus, &.ui-state-active {
				background: #e5e5e5;
				margin: 0;
			}
		}
	}
}


