.menu {
  text-align: center;
}
#top-menu {
  margin: 0;
  text-align: center;
  > li {
    display: inline-block;
    vertical-align: middle;
    &.current {
      > a {
       color: $brand-secondary;
     }
   }
   > a {
    padding: 15px 20px;
    font: 400 18px/22px  $font-family;
    color: $light-color;
    display: block;
    position: relative;
    text-transform: capitalize;
    white-space: normal;
    &:hover {
      color:$brand-secondary;
    }
  }
  .sub-menu {
    &.collapse {
      display: none;
    }
    min-width: 200px;
    background: $light-color;
    padding: 15px;
    top: 100%;
    position: absolute;
    z-index: 18;
    border-radius: 5px;
    @include media-breakpoint-up(lg) { /* min 992 */
     border-top: 3px solid $brand-secondary;
     box-shadow: 0 2px 5px 1px rgba(0,0,0,.1);
     &:after {
      color: $brand-secondary;
      content: "\E5C7";
      font-family: "Material Icons";
      font-size: 45px;
      right: 0;
      position: absolute;
      top: -13px;
      right: auto;
    }
  }
  @include media-breakpoint-up(lg) { /* min 992 */
    opacity: 0;
    display: block !important;
    text-align: left;
    -webkit-transform-origin:0 0 0;
    transform-origin: 0 0 0;
    -webkit-transform: scale(1,0);
    -moz-transform: scale(1,0);
    -ms-transform: scale(1,0);
    -o-transform: scale(1,0);
    transform: scale(1,0);
    -webkit-transition: all 500ms ease 0s;
    -moz-transition: all 500ms ease 0s;
    -ms-transition: all 500ms ease 0s;
    -o-transition: all 500ms ease 0s;
    transition: all 500ms ease 0s;
  }
  .top-menu[data-depth="2"] {
    .child-menu {
      padding: 0 0 0 10px;
    }
  }
  ul[data-depth="1"]  {
    > li {
      display: inline-block;
      vertical-align: top;
      width: 160px;
      padding: 10px;
      > a {
        display: block;
        position: relative;
        font-size: 14px;
        line-height: 16px;
        font-weight: 600;
        padding: 0 0 10px;
        text-transform: uppercase;
        white-space: normal;
        font-family: $font-family;
        transition: all 0.5s ease 0s;
        color: $brand-primary;
        &:hover {
          color: $brand-secondary;
          transition: all 0.5s ease 0s;
        }
        &:after {
          content: "";
          height: 1px;
          background: $gray;
          width: 60px;
          bottom: 0;
          position: absolute;
          left: 0;
          @include media-breakpoint-down(md) { /* max 991 */
            display: none;
          }
        }
      }
      .child-menu {
        li {
          a {
            display: block;
            font-size: 14px;
            font-weight: normal;
            line-height: 16px;
            padding: 0 0 10px;
            white-space: normal;
            position: relative;
            color:$gray;
            transition: all 0.5s ease 0s;
            &:hover {
              color: $brand-secondary;
              transition: all 0.5s ease 0s;
            }
          }
        }
      }
    }
  }
  .category-thumbnail {
    padding: 10px;
    .cat-img {
      max-width: 100%;
    }
  }
}
&:hover {
  > a {
    color:$brand-secondary;
    webkit-transition: all 0.7s ease 0s;
    -moz-transition: all 0.7s ease 0s;
    -ms-transition: all 0.7s ease 0s;
    -o-transition: all 0.7s ease 0s;
    transition: all 0.7s ease 0s;
  }
  .sub-menu {
    @include media-breakpoint-up(lg) { /* min 992 */
      opacity: 1;
      -webkit-transform: scale(1,1);
      -moz-transform: scale(1,1);
      -ms-transform: scale(1,1);
      -o-transform: scale(1,1);
      transform: scale(1,1);
    }
  }
}
}
.collapse {
  display: block;
  padding-top: 10px;
}
}

#_desktop_top_menu {
  #top-menu {
    > li {
      .sub-menu {
        &.onecolumn {
          width: 234px;
        }
        &.twocolumn {
          width: 442px;
        }
        &.threecolumn {
          width: 520px;
        }
        &.fourcolumn {
          width: 843px;
        }
      }
    }
  }
}
.menu-icon {
  float: left;
  cursor: pointer;
  .cat-title {
    cursor: pointer;
    display: block;
    font: 16px/20px $font-custom;
    padding: 20px 15px;
    position: relative;
    text-transform: uppercase;
    color: $light-color;
     @media (max-width:540px) and (min-width:320px) {
      padding: 20px 10px;
     }
    &:hover{
      color: $gray-dark-color;
    }
    &:before {
      content: "\f0C9";
      display: inline-block;
      font-family: "FontAwesome";
      font-size: 17px;
      margin: 0 10px 0 0;
    }
    span {
      @media (max-width:540px) and (min-width:320px) {
        display: none;
      }
    }
  }
  &.rvclose {
    .cat-title {
      color: $gray-darker;
      &:before {
        content: "\f00D";
      }
    }
  }
}
#menuCanvas {
  background-color: rgba(0, 0, 0, 0.9);
  height: 100%;
  left: 0;
  opacity: 0;
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  text-align: center;
  top: 0;
  transition: opacity 1s ease 0s;
  width: 0;
  z-index: 8;
}
#mobile_top_menu_wrapper {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  background-color: #FFF;
  webkit-transition: all 0.2s ease 0s;
  -khtml-transition: all 0.2s ease 0s;
  -moz-transition: all 0.2s ease 0s;
  -o-transition: all 0.2s ease 0s;
  transition:all 0.2s ease 0s;
  transform: translateX(-100%);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  &.menu-visible {
    webkit-transition: all 0.2s ease 0s;
  -khtml-transition: all 0.2s ease 0s;
  -moz-transition: all 0.2s ease 0s;
  -o-transition: all 0.2s ease 0s;
  transition:all 0.2s ease 0s;
    transform : translateX(0%);
  }
  #top-menu {
    background: $light-color;
    text-align: left;
    padding: 0;
    > li {
      display: block;
      position: relative;
      border-bottom: 1px solid $base-border-color;
      > a {
        color: $gray-darker;
        position: relative;
        text-decoration: none;
        display: block;
        padding: 10px 0;
        line-height: 20px;
        font-size: 13px;
        text-transform: capitalize;
        &:hover {
          color: $brand-primary;
        }
      }
      .sub-menu {
        border: medium none;
        padding: 0px;
        position: relative;
        .top-menu {
          padding: 0 0 0 15px;
        }
        .top-menu[data-depth="2"] {
          .child-menu {
            padding: 0;
          }
        }
        ul[data-depth="1"]  {
          > li {
            display: block;
            width: 100%;
            padding: 0;
            border-bottom: 1px solid $base-border-color;
            > a {
              padding: 10px 0;
              line-height: 20px;
              font-weight: normal;
              text-transform: capitalize;
            }
            .child-menu {
              li {
                border-bottom: 1px solid $base-border-color;
                a {
                  padding: 10px 0;
                  line-height: 20px;
                  text-transform: capitalize;
                }
                &:first-child {
                  border-top: 1px solid $base-border-color;
                }
                &:last-child {
                  border-bottom: none;
                }
              }
            }
            &:first-child {
              border-top: 1px solid $base-border-color;
            }
            &:last-child {
              border-bottom: none;
            }
          }
        }
      }
      &:first-child {
        border-top: 1px solid $base-border-color;
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
  .top-menu {
    color: $gray;
    .collapse-icons[aria-expanded="true"] {
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    .collapse-icons .remove {
      display: none;
    }
    .navbar-toggler {
      display: inline-block;
      font-size: 16px;
      height: 30px;
      padding: 4px;
      position: absolute;
      right: 0;
      text-align: center;
      top: 6px;
      width: 30px;
    }
    a[data-depth="0"] {
      padding: 10px 0;
    }
    .collapse {
      display: none;
      padding-top: 0px;
      &.in {
        display: block;
      }
    }
    .sub-menu {
      &.collapse {
        display: none;
        &.in {
          display: block;
        }
      }
    }
  }
  .header-collapse {
    position: absolute;
    overflow: scroll;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 0;
    padding: 0;
    .menu-close {
      line-height: 1;
      position: absolute;
      right: 15px;
      top: 15px;
      left: auto;
      z-index: 999;
      cursor: pointer;
      overflow: hidden;
      i {
        font-size: 22px;
        color: $gray-darker;
      }
    }
    .rvmenu {
      padding: 0 15px;
      margin-bottom: 20px;
      border-bottom: 1px solid #e0e0e0;
      .cat-title {
        display: block;
        padding: 0 0 15px;
        font-weight: 600;
        text-transform: uppercase;
        cursor: pointer;
        margin: 0;
        color: #000;
        font-size: 14px;
        &:before {
          content: "\F0C9";
          font-family: "FontAwesome";
          font-weight: 400;
          margin: 0 5px 0 0;
          display: inline-block;
        }
      }
    }
    .responsive-content {
      padding: 0 15px;
      margin-bottom: 30px;
      #_mobile_wishlist {
        margin: 0 0 10px;
        .wishlist-logo {
          color: $gray-darker;
          i {
            display: none;
          }
          &:hover {
            color: $brand-secondary;
          }
        }
      }
      #_mobile_compare {
        margin: 0 0 10px;
        .compare {
          .bt_compare {
            color: $gray-darker;
            padding: 0;
            i {
              display: none;
            }
            &:hover {
              color: $brand-secondary;
            }
            &:after {
              display: none;
            }
          }
        }
      }
      #_mobile_headercontact {
        margin: 0 0 10px;
        .contact-icon {
          display: none;
        }
        .contact-info {
          display: inline-block;
          vertical-align: middle;
          text-align: left;
          .contact-title { 
            color: $brand-primary;
            display: inline-block;
            font: "font-family";
            font-size: 16px;
            line-height: normal;
            text-transform: capitalize;
            display: none;
          }
          .contact-number {
            i {
              font-size: 22px;
              color: #25D366;
              vertical-align: middle;
              margin-right: 5px;
            }
            a {
              color: $gray-darker;
              font-size: 14px;
              vertical-align: middle;
              -webkit-transition: all 0.3s ease 0s;
              -moz-transition: all 0.3s ease 0s;
              -ms-transition: all 0.3s ease 0s;
              -o-transition: all 0.3s ease 0s;
              transition: all 0.3s ease 0s;
            }
          } 
        }
        &:hover {
          .contact-icon {
            transform: rotateY(0deg);
          }
          .contact-number {
            a {
              color: $brand-secondary;
            }
          }
        }
      }
    }
  }
}




