#smartblog_block {
	position: relative;
	margin-bottom: 30px;
	border-radius: 10px;
	.products_block_inner {
		text-align: left;
		overflow: hidden;
	}
	.sdsblog-box-content {
		margin: 0 -15px;
		#smartblog-carousel {
			.item {
				padding: 0 15px;
				.blog_post {
					display: inline-block;
					display: inline-block;
					background: #fff;
					padding: 20px;
					border-radius: 10px;
					.news_module_image_holder  {
						overflow: hidden;
						position: relative;
						display: block;
						margin: 0 0 15px;
						border-radius: 5px; 
						a {
							display: block;
							position: relative;
							img {
								max-width: 100%;
								width: 100%;
							}
							
						}
						.blog-hover {
							height: 100%;
							left: 0;
							position: absolute;
							top: 0;
							-webkit-transition: all 0.7s ease 0s;
							-o-transition: all 0.7s ease 0s;
							transition: all 0.7s ease 0s;
							width: 100%;
							@include media-breakpoint-down(xs) { /* max 543 */
							}
						}
						.blogicons {
							bottom: 0;
							height: 45px;
							left: 0;
							margin: auto;
							position: absolute;
							right: 0;
							transition: all 0.3s ease 0s;
							-moz-transition: all 0.3s ease 0s;
							-webkit-transition: all 0.3s ease 0s;
							-khtml-transition: all 0.3s ease 0s;
							-o-transition: all 0.3s ease 0s;
							top: 0;
							width: 45px;
							opacity: 0;
							.icon {
								display: inline-block;
								height: 45px;
								width: 45px;
								position: relative;
								&:before {
									font-family: "FontAwesome";
									content: "";
									border: 2px solid $light-color;
									color: $light-color;
									font-size: 15px;
									height: 45px;
									margin: auto;
									left: 0;
									position: absolute;
									padding: 12px 14px;
									border-radius: 50%;
									transition: all 0.3s ease 0s;
									-moz-transition: all 0.3s ease 0s;
									-webkit-transition: all 0.3s ease 0s;
									-khtml-transition: all 0.3s ease 0s;
									-o-transition: all 0.3s ease 0s;
									top: 0;
									width: 45px;
									-moz-transform: scale(1.5);
									-webkit-transform: scale(1.5);
									-o-transform: scale(1.5);
									-ms-transform: scale(1.5);
									transform: scale(1.5);

								}
								&.zoom {
									&:before {
										content: "\F002";
									}
								} 
								&:hover{
									&:before{
										transition: all 0.3s ease 0s;
										-moz-transition: all 0.3s ease 0s;
										-webkit-transition: all 0.3s ease 0s;
										-khtml-transition: all 0.3s ease 0s;
										-o-transition: all 0.3s ease 0s;
										border-color: $brand-secondary;
										background: $brand-secondary;	
										color: $brand-primary;
									}
								}
							}
						}
						.smartbloginfo {
							position: absolute;
							left: 0;
							right: auto;
							bottom: 20px;
							opacity: 0;
							-webkit-transition: all 0.7s ease 0s;
							-o-transition: all 0.7s ease 0s;
							transition: all 0.7s ease 0s;
							transform: translateX(-100%);
							.blog_date {
								margin: 0;
								padding: 5px 30px;;
								border-radius: 0 25px 25px 0;
								background: $brand-secondary;
								display: inline-block;
								vertical-align: middle;
								font: 400 16px/18px $font-custom;
								color: $brand-primary;
								.day_date, .day_month, .day_year {
									display: inline-block;
									vertical-align: middle;
									margin: 0 2px 5px 0;
								}
								i {
									display: none;
								}
							}
						}
					}
					.blog_content {
						text-align: left;
						.blog_inner {
							.author{
								display: inline-block;
								vertical-align: middle;
							}
							.sds_post_title {
								margin: 0 0 5px;
								font-weight: 500;
								a{
									color: $gray-darker;
									font-size: 16px;
									line-height: 16px;
									margin: 0;
									padding: 0px 0;
									overflow: hidden;
									white-space: nowrap;
									text-overflow: ellipsis;
									transition: 0.3s all ease;
									&:hover{
										color: $brand-secondary;
										transition: all 0.3s ease 0s;
										-webkit-transition: all 0.3s ease 0s;
										-moz-transition: all 0.3s ease 0s;
										-ms-transition: all 0.3s ease 0s;
										-o-transition: all 0.3s ease 0s;
									}
								}
							}
							.desc {
								margin: 0 0 10px;
								text-align: left;
								letter-spacing: 0.8px;
								line-height: 20px;
								color: $gray;
								font-size: 12px;
								line-height: 18px;
								text-transform: capitalize;
							}
							.read_more {
								.readmore {
									text-transform: capitalize;
									&:after {
										content: "\E409";
										font-family: material icons;
										vertical-align: middle;
									}
								}
							}
							.smartbloginfo {
								margin: 0 0 10px;
								.view {
									
								}
								.comment {

								}
							}
							.r_more {
								background: transparent none repeat scroll 0 0;
								color: $gray;
								padding: 8px 15px;
								border: 1px solid #e6e6e6;
								display: inline-block;
								font-weight: 400;
								font-size: 14px;
								line-height: 20px;
								text-transform: capitalize;
								transition:all 0.5s ease 0s;
								-webkit-transition: all 0.5s ease 0s;
								-moz-transition: all 0.5s ease 0s;
								-ms-transition: all 0.5s ease 0s;
								-o-transition: all 0.5s ease 0s;
								&:hover{
									background: $brand-primary;
									color: $light-color;
									border-color: $brand-primary;
								}
							}
						}
					}
				}
				&:hover {
					.news_module_image_holder {
						.blogicons {
							opacity: 1;
							transition: all 0.3s ease 0s;
							-moz-transition: all 0.3s ease 0s;
							-webkit-transition: all 0.3s ease 0s;
							-khtml-transition: all 0.3s ease 0s;
							-o-transition: all 0.3s ease 0s;
							.icon {
								&:before {
									-moz-transform: scale(1);
									-webkit-transform: scale(1);
									-o-transform: scale(1);
									-ms-transform: scale(1);
									transform: scale(1);
									-moz-transition: all 0.3s ease 0s;
									-webkit-transition: all 0.3s ease 0s;
									-khtml-transition: all 0.3s ease 0s;
									-o-transition: all 0.3s ease 0s;
									transition: all 0.3s ease 0s;
								}
							}
						}
						.blog-hover {
							background: rgba(0,0,0,.4);
							-webkit-transition: all 0.7s ease 0s;
							-o-transition: all 0.7s ease 0s;
							transition: all 0.7s ease 0s;
						}	

						.smartbloginfo {
							opacity: 1;
							-webkit-transition: all 0.7s ease 0s;
							-o-transition: all 0.7s ease 0s;
							transition: all 0.7s ease 0s;
							transform: translateX(0%);
						}
					}
				}
			}
		}
		.owl-nav {
			margin: 0;
			position: absolute;
			right: 54px;
			top: -60px;
		}
	}
}

#sdsblogArticle{
	margin-bottom: 20px;
	text-align: center;
	#lipsum {
		width: 100%;
		img {
			max-width: 100%;
			width: 100%;
			border-radius: 5px;
		}
	}
	.page-item-title{
		margin-bottom: 30px;
		h1{
			margin: 15px 0;
			color: $brand-primary;
			font: 500 22px/22px $font-family;
			padding: 0;
			text-transform: uppercase;
			width: 100%;
			-moz-transition: all 0.3s ease 0s;
			-webkit-transition: all 0.3s ease 0s;
			-khtml-transition: all 0.3s ease 0s;
			-o-transition: all 0.3s ease 0s;
			transition: all 0.3s ease 0s;
			cursor: pointer;
			&:hover{
				color: $brand-secondary;
			}
		}
	}
	.post-info {
		display: block;
		margin-bottom: 30px;
		.articleSection {
		}
		.author {
		}
		.comment {
		}
		.dateCreated {
			
		}
		.articleSection {
			a {
				color: $brand-primary;
				&:hover {
					color: $brand-secondary;
				}
			}
		}
		span{
			span {
				font-size: 15px;
				display: inline-block;
				margin-right: 10px;
				position: relative;
				text-transform: capitalize;
				color: $brand-primary;
				font-family: $font-family;
				margin-bottom: 5px;
				.fa {
					padding-right: 0px;
				}
			}
		}

	}
	.sdsarticle-des {
		padding-top: 30px;
		padding-bottom: 20px;
		font-size: 15px;
		text-align: center;
		line-height: 25px;
		letter-spacing: .5px;
		font-family: $font-family;
		margin: 0 auto;
		width: 100%;
	}
}

#respond {
	color: $gray;
	.comment-reply-title {
		font: 600 20px/20px $font-family;
		margin: 0 0 20px;
		border-bottom: 1px solid $gray-light;
		color: $brand-primary;
		padding: 0 0 10px; 
	} 
	#commentInput {
		text-align: left;
		.right {
			padding: 10px 0 0;
			text-align: right;
			#submitComment {
				font: 500 14px $font-family;
				padding: 5px 15px;
				text-transform: capitalize;
				color: $light-color;
				background: $brand-primary;
				border-radius: 5px;
				-moz-transition: all 0.3s ease 0s;
				-webkit-transition: all 0.3s ease 0s;
				-khtml-transition: all 0.3s ease 0s;
				-o-transition: all 0.3s ease 0s;
				transition: all 0.3s ease 0s;
				&:hover {
					background: $gray-dark-color;
					color: $light-color;
				}
			}
		}
	}
	input[disabled] {
		background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
		border: medium none;
	}
	.captcha input[type="text"] {
		max-width: 85px;
	}
	td {
		padding: 5px;
		vertical-align: top;
		&:first-child {
			padding-top: 8px;
		}
	}
}

.smartblogcomments {
	.success {
		background-color: #DFF0D8;
		border-color: #D6E9C6;
		color: #3C763D;
		border: 1px solid rgba(0, 0, 0, 0);
		border-radius: 4px;
		margin-bottom: 20px;
		padding: 15px;
	}
	.attention {
		background-color: #f5f5f5;
		border-color: #e5e5e5;
		color: #333;
		border: 1px solid rgba(0, 0, 0, 0);
		border-radius: 4px;
		margin-bottom: 20px;
		padding: 15px;
	}
	.warning {
		background-color: #F2DEDE;
		border-color: #EBCCD1;
		color: #A94442;
		border: 1px solid rgba(0, 0, 0, 0);
		border-radius: 4px;
		margin-bottom: 20px;
		padding: 15px;
	}
}

#articleComments {
	border-bottom: 1px solid #e5e5e5;
	border-top:1px solid #e5e5e5;
	margin: 0px 0 30px;
	#comments {
		margin-bottom:10px;
	}
	.commenttitle {
		font: 400 14px $font-family;
		margin: 10px 0;
	}
	.commentList {
		margin: 0;
		position: relative;
		li {
			padding: 8px 0 8px 80px;
			.comment-reply-link {
				font: 600 11px/16px $font-family;
				padding: 6px 10px;
			}
			.avatar {
				background: none;
				padding: 0;
				left: 10px;
				position: absolute;
				top: 10px;
			}
			.reply {
				height: auto;
				text-align: right;
			}
			.name, .name a {
				font-size: 13px;
				font-weight: bold;
			}
			.created{
				margin:  0 0 10px;
			}
			p{
				font-size: 15px;
			}
		}
	}
}

#sdsblogCategory {
	margin: 0 0 30px;
	.imageFeatured {
		background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
		border: medium none;
		margin: 0 0 20px 0;
		max-width: 100%;
		padding: 0;
	}
	.catDesc {
		margin: 0 0 20px;
	}
}

.blog_pagination {
	.pagination {
		text-align: center;
		.pagination_bottom {
			float: right;
			margin-bottom: 0;
			@include media-breakpoint-down(sm) { /* max 767 */
				width: 100%;
			}
			li {
				display: inline-block;
				height: rem(35px);
				width: rem(35px);
				vertical-align: middle;
				border: 1px solid $base-border-color;
				a {
					color: black;
					font-weight: 600;
					line-height: rem(35px);
					cursor: pointer;
					display: block;
					&.disabled {
						color: $gray;
						cursor: not-allowed;
					}
					&.previous, &.next {
						font-size: 0;
						i {
							font-size: rem(20px);
						}
					}
				}
				&.current a {
					cursor: default;
				}
				&.current, &:hover {
					border-color: $brand-primary;
					a {
						color: $brand-primary;
						text-decoration: none;
					}
				}
			}
		}
		.results { 
			line-height: 35px;
			float: left;
			@include media-breakpoint-down(sm) { /* max 767 */
				text-align: center;
				width: 100%;
			}
		}
	}
}

