.quickview {
  .modal-dialog {
    width: calc(100% - 30px);
    max-width: rem(1024px);
  }
  .modal-content {
    position: relative;
    background: $light-background;
    .modal-header {
      position: absolute;
      right: 10px;
      top: 10px;
      border: none;
      padding: 0;
      z-index: 1;
    }
    .modal-body {
      .images-container {
        width: 100%;
        position: relative;
        @include media-breakpoint-down(sm) { /* max 767 */
          max-width: 400px;
          margin: 0 auto;
        }
        @include media-breakpoint-down(xs) { /* max 543 */
          max-width: 250px;
          margin: 0 auto;
        }
      }
    }
    .modal-footer {
      border-top: 1px solid rgba($gray,.3);
      text-align: center;
      .social-sharing {
        margin-top: 0;
      }
    }
    .layer {
      display: none;
    }
  }
}


