#rvmanufacturerblock {
	position: relative;
	background-color: $light-color;
	margin-bottom: 30px;
	border-radius: 10px;
	.homepage-heading {
		display: none;
	}
	.owl-nav{
		margin: 0;
		position: absolute;
		right: 20px;
		top: -42px;
		display: none;
		.owl-prev {
			right: 2px;
			i {
				position: absolute;
				&:before {
					content: "\f104";
				}
			}
			&:after {
				content: '|';
				color: $gray-darker;
				font-size: 14px;
				padding: 0 20px;
				vertical-align: top;
				position: relative;
				top: 3px;
			}
		}
		.owl-next {
			left: 2px;
			i {
				position: absolute;
				&:before {
					content: "\f105";
				}
			}
		}
		> div {
			color: $gray-darker;
			border: none;
			background: transparent;
			font-size: 22px;
			height: 20px;
			margin: -10px 0 0 0px;
			position: absolute;
			text-align: center;
			transition: all 0.3s ease 0s;
			width: 20px;
			-webkit-transition: all 0.3s ease 0s;
			-moz-transition: all 0.3s ease 0s;
			-ms-transition: all 0.3s ease 0s;
			-o-transition: all 0.3s ease 0s;
			transition: all 0.3s ease 0s;
			&.disabled {
				opacity: 0.4;
			}
			i {

			}
			&:hover {
				-webkit-transition: all 0.3s ease 0s;
				-moz-transition: all 0.3s ease 0s;
				-ms-transition: all 0.3s ease 0s;
				-o-transition: all 0.3s ease 0s;
				transition: all 0.3s ease 0s;
				background: transparent;
				border: none;
				color: $brand-primary;
			}
		}
	}
	.block_content {
		padding: 30px 15px; 
	}
	#manufacturer-carousel {
		margin-bottom: 0;
		.item {
			text-align: center;
			padding: 0 15px;
			.manufacturer_image {
				img {
					max-width: 100%;
					opacity: 0.8;
					-webkit-transition: all 0.3s ease 0s;
					-moz-transition: all 0.3s ease 0s;
					-ms-transition: all 0.3s ease 0s;
					-o-transition: all 0.3s ease 0s;
					transition: all 0.3s ease 0s;
				}
			}
			&:hover {
				.manufacturer_image {
					img {
						opacity: 0.6;
					}
				}	
			}
		}
	}
}


