#rvparallaxcmsblock {
	position: relative;
	margin-bottom: 30px; 
	.parallax {
		border-radius: 10px; 
		.parallax_inner {
			.parallax_text {
				text-align: center;
				padding: 106px 0;
				color: $light-color;
				position: relative;
				overflow: hidden;
				display: block;
				@media (max-width: 1366px) {
					padding: 133px 0;
				}
				@include media-breakpoint-down(md) { /* max 991 */
					padding: 70px 0;
				}
				@include media-breakpoint-down(xs) { /* max 543 */
					
				}
				@media (max-width:530px) and (min-width:320px) {
					
				}
				.text_maintitle {
					font: 500 30px/30px  $font-family;
					text-transform: uppercase;
					letter-spacing: 2px;
					position: relative;
					display: block;
					margin-bottom: 30px; 
					@include media-breakpoint-down(md) { /* max 991 */
						
					}
					@media (max-width:530px) and (min-width:320px) {
						font-size: 28px;
					}
					&:after {
						content: "";
						height: 5px;
						background: rgba(0, 0, 0, 0) linear-gradient(to right, rgba(0, 0, 0, 0) 0%, #c19d67 50%, rgba(0, 0, 0, 0) 100%) repeat scroll 0 0;
						left: 230px;
						right: 230px;
						bottom: -15px;
						position: absolute;
						@media (max-width:530px) and (min-width:320px) {
							left: 20px;
							right: 20px;
						}
					}
				}
				.sub_maintitle {
					font: 500 15px/20px  $font-family;
					text-transform: capitalize;
					letter-spacing: 0.5px;
					position: relative;
					display: block;
					margin-bottom: 15px; 
					@include media-breakpoint-down(md) { /* max 991 */
					}
				}
				.parallax_button {
					display: inline-block;
					vertical-align: middle;
					a {
						font-family: $font-custom;
						border: 1px solid transparent;
						color: $light-color;
						background: transparent;
						display: inline-block;
						padding: 7px 22px;
						vertical-align: middle;
						text-transform: capitalize;
						-webkit-transition: all 0.3s ease 0s;
						-moz-transition: all 0.3s ease 0s;
						-ms-transition: all 0.3s ease 0s;
						-o-transition: all 0.3s ease 0s;
						transition: all 0.3s ease 0s;
						text-transform: uppercase;
						position: relative;
						font-weight: 600;
						&:after {
							content: "\f0da";
							font-family: $font-icon;
							font-size: 18px;
							color: $brand-secondary;
							position: absolute;
							right: 5px;
							top: 7px;
						}
						&:hover {
							border: 1px solid transparent;
							color: $brand-secondary;
							transition-duration: all 0.3s ease 0s;
						}
					}
				}
			}
		}
	}
}

#rvcmsblock {
	margin-bottom: 30px; 
	float: left;
	margin-top: 0;
	padding: 0;
	width: 100%;
	.rvbannerinner {
		text-align: center;
		.bannercms-container {
			> a {
				display: inline-block;
				vertical-align: middle;
				overflow: hidden;
				position: relative;
				transition: all 0.5s ease 0s;
				&:before {
					bottom: 0;
					content: "";
					left: 50%;
					opacity: 1;
					-moz-opacity: 1;
					-khtml-opacity: 1;
					-webkit-opacity: 1;
					position: absolute;
					right: 51%;
					top: 0;
					background-color: rgba(255, 255, 255, 0.3);
				}
				&:after{
					bottom: 50%;
					content: "";
					left: 0;
					opacity: 1;
					-moz-opacity: 1;
					-khtml-opacity: 1;
					-webkit-opacity: 1;
					position: absolute;
					right: 0;
					top: 50%;
					background-color: rgba(255, 255, 255, 0.3);
				}
				img {
					max-width: 100%;
				}
				&:hover {
					&:before {
						left: 0;
						opacity: 0;
						-moz-opacity: 0;
						-khtml-opacity: 0;
						-webkit-opacity: 0;
						right: 0;
						transition: all 900ms ease-in-out 0s;
						-webkit-transition: all 900ms ease-in-out 0s;
						-moz-transition: all 900ms ease-in-out 0s;
						-o-transition: all 900ms ease-in-out 0s;
						-ms-transition: all 900ms ease-in-out 0s;
					}
					&:after {
						bottom: 0;
						opacity: 0;
						-moz-opacity: 0;
						-khtml-opacity: 0;
						-webkit-opacity: 0;
						top: 0;
						transition: all 900ms ease-in-out 0s;
						-webkit-transition: all 900ms ease-in-out 0s;
						-moz-transition: all 900ms ease-in-out 0s;
						-o-transition: all 900ms ease-in-out 0s;
						-ms-transition: all 900ms ease-in-out 0s;
					}
				}
			}  
		}
	}
}
