#rvservices {
	margin-bottom: 30px;
	position: relative;
	.main_content {
		background: $light-color;
		border-radius: 10px;
		padding: 25px 15px;
		text-align: center;
		#rvservice-carousel {
			position: relative;
			.owl-item {
				@media (max-width:530px) and (min-width:320px) {
					text-align: center; 
				}
				.service-inner {
					cursor: pointer;
					display: inline-block;
					vertical-align: middle;
					.service-img {
						vertical-align: middle;
						display: inline-block;
						margin: 0 10px 0 0;
						transform-style: preserve-3d;
						transition: all 1.0s linear;
						@media (max-width:530px) and (min-width:320px) {
							margin: 0;
						}
					}
					.service-content {
						margin: 10px 0 0;
						overflow: hidden;
						vertical-align: middle;
						display: inline-block;
						text-align: left;
						@media (max-width:530px) and (min-width:320px) {
							display: block;
							vertical-align: middle;
							text-align: center;
						}
						.service-text {
							font-size: 16px;
							color: $brand-primary;
							cursor: pointer;
							margin-bottom: 5px;
							font-family: $font-custom;
							font-weight: 500;
							text-transform: capitalize;
							-webkit-transition: all 0.5s ease 0s;
							-moz-transition: all 0.5s ease 0s;
							-ms-transition: all 0.5s ease 0s;
							-o-transition: all 0.5s ease 0s;
							transition: all 0.5s ease 0s;
							@include media-breakpoint-down(md) { /* max 991 */
								font-size: 14px;
							}
						}
						.service-desc {
							font-size: 14px;
							color: $gray;
							font-family: $font-family;
							font-weight: 400;
							@include media-breakpoint-down(md) { /* max 991 */
								font-size: 12px;
							}	
						}
					}
					&:hover {
						.service-img { 
							transform: rotateY(360deg);
						}
						.service-content {
							.service-text {
								color:  $brand-secondary;
								-webkit-transition: all 0.5s ease 0s;
								-moz-transition: all 0.5s ease 0s;
								-ms-transition: all 0.5s ease 0s;
								-o-transition: all 0.5s ease 0s;
								transition: all 0.5s ease 0s;
							}
						}
					}
				}
			}
			.owl-nav {
				display: none;
			}
			.owl-dots {
				display: none;
			}
		}
	}
}

@-webkit-keyframes sonarEffect {
	0% {
		opacity: 0.3;
	}
	40% {
		opacity: 0.5;
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
	}
	100% {
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
		-webkit-transform: scale(1.5);
		opacity: 0;
	}
}
@-moz-keyframes sonarEffect {
	0% {
		opacity: 0.3;
	}
	40% {
		opacity: 0.5;
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
	}
	100% {
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
		-moz-transform: scale(1.5);
		opacity: 0;
	}
}
@keyframes sonarEffect {
	0% {
		opacity: 0.3;
	}
	40% {
		opacity: 0.5;
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
	}
	100% {
		box-shadow: 0 0 0 2px rgba(255,255,255,0.1), 0 0 10px 10px $brand-primary, 0 0 0 10px rgba(255,255,255,0.5);
		transform: scale(1.5);
		opacity: 0;
	}
}



