
.rv-container-menu {
	background: #926dde;
	height: 50px;
}
.container_rv_megamenu {
	padding: 0 15px 0 0;
	position: relative;
	width:21%;
	border:none;
	@media (max-width: 1199px) {
		width:23%;
	}
	@include media-breakpoint-down(md) { /* max 991 */
		padding: 0;
		width: auto;
		position:static;
		float:left;
		display: none;
	}
	.rv-menu-horizontal {
		float: left;
		width: 90%;
		position: relative;
		@include media-breakpoint-down(md) { /* max 991 */
			position: static;
			background:none;
			padding:0;
			box-shadow: none;
			-moz-box-shadow: none;
			-webkit-box-shadow: none;
		}
		ul {
			li {
				a {
					span {
						@include media-breakpoint-only(md) { /* only 991 */
							text-transform: capitalize;
							float: left;
						}
					}
				}
				&.parent {
					.icon-drop-mobile {
						@include media-breakpoint-down(md) { /* max 991 */
							float: right;
							height: 15px;
							margin: auto;
							position: absolute;
							right: 15px;
							top: 10px;
							width: 15px;
							display: block !important;
						}
						&:after {
							@include media-breakpoint-down(md) { /* max 991 */
								content: "\E313";
								font-family: "Material Icons";
								font-weight: normal;
								font-style: normal;
								text-decoration: inherit;
								-webkit-font-smoothing: antialiased;
								font-size: 18px;
								color: #666;
								cursor: pointer;
							}
						}
					}
					&.active {
						.icon-drop-mobile {
							&:after {
								content: "\E316";
							}
						}
					}
				}
				&.item-header {
					> a {
						@media (max-width: 1199px) {
							font-size: 15px;
						}
						@include media-breakpoint-only(md) { /* only 991 */
							color: $gray-darker !important;
						}
					}
				}
				&.level-1 {
					float: left;
					display: inline-block;
					-webkit-transition: all 0.5s ease 0s;
					-moz-transition: all 0.5s ease 0s;
					-ms-transition: all 0.5s ease 0s;
					-o-transition: all 0.5s ease 0s;
					transition: all 0.5s ease 0s;
					width: 100%;
					position: relative;
					cursor: pointer;
					@include media-breakpoint-down(md) { /* max 991 */
						cursor: pointer;
						display: block!important;
						float: left;
						position: relative;
						width: 100%;
						overflow: hidden;
					}
					&:after {
						@include media-breakpoint-up(md) { /* min 992 */
							border-bottom: 1px solid #e5e5e5;
							bottom: 0;
							content: "";
							left: 0;
							margin: 0 auto;
							position: absolute;
							right: 0;
							width: 90%;
						}
					}
					&.more {
						@include media-breakpoint-down(md) { /* max 991 */
							display: none!important;
						}
					}
					> a {
						color: #555;
						font: 500 15px/15px $font-family;
						padding: 12px 20px;
						position: relative;
						float: left;
						width: 100%;
						text-align: left;
						text-transform: capitalize;
						@media (min-width: 1200px) and (max-width: 1299px) {
							padding: 12px 13px;
						}
						@media (min-width: 992px) and (max-width: 1199px) {
							padding: 12px 10px;
						}
						@media (max-width: 1199px) {
							font-size: 15px;
						}
						@include media-breakpoint-down(md) { /* max 991 */
							padding: 6px 15px;
							line-height:21px;
						}
						span {
							display: inline-block;
							vertical-align: top;
							-webkit-transform: translateZ(0);
							transform: translateZ(0);
							box-shadow: 0 0 1px rgba(0, 0, 0, 0);
							-webkit-backface-visibility: hidden;
							backface-visibility: hidden;
							-moz-osx-font-smoothing: grayscale;
							position: relative;
							overflow: hidden;
							float: left;
							color: $brand-primary;
							-webkit-transition: all 0.3s ease 0s;
							-moz-transition: all 0.3s ease 0s;
							-ms-transition: all 0.3s ease 0s;
							-o-transition: all 0.3s ease 0s;
							transition: all 0.3s ease 0s;
							&:hover{
								color:$brand-secondary;
								-webkit-transition: all 0.3s ease 0s;
								-moz-transition: all 0.3s ease 0s;
								-ms-transition: all 0.3s ease 0s;
								-o-transition: all 0.3s ease 0s;
								transition: all 0.3s ease 0s;
							}
						}
						.img-icon {
							max-width: 20px;
							float: left;
							margin:2px 8px 0 0px;
						}
						i {
							@include media-breakpoint-only(md) { /* only 991 */
								float: left;
								font-size: 15px;
								font-weight: normal;
								line-height: 20px;
								padding: 0 5px 0 0;
								color:#414141;
							}
						}
					} 
					.more-menu {
						&.active {
							-webkit-transition: all 0.5s ease 0s;
							-moz-transition: all 0.5s ease 0s;
							-ms-transition: all 0.5s ease 0s;
							-o-transition: all 0.5s ease 0s;
							transition: all 0.5s ease 0s;
						}
						.categories {
							color: #555;
							font: 400 16px/24px $font-family;
							padding: 12px 20px;
							position: relative;
							float:left;
							padding: 12px 20px 12px 50px;
							width: 100%;
							text-align: left;
							text-transform: capitalize;
							@media (min-width: 1200px) and (max-width: 1299px) {
								padding: 12px 13px 12px 40px !important;
							}
							@media (min-width: 992px) and (max-width: 1199px) {
								padding: 12px 12px 12px 40px !important;
							}
							@media (max-width: 1199px) {
								font-size:15px;
							}
							@include media-breakpoint-down(md) { /* max 991 */
								padding: 6px 20px 6px 45px;
							}
							&:before {
								bottom: auto;
								content: "\e5d3";
								font-family: "Material Icons";
								font-size: 20px;
								left:20px;
								position: absolute;
								right: auto;
								top:11px;
								color:#888;
								@media (min-width: 1200px) and (max-width: 1299px) {
									left: 14px;
								}
								@media (min-width: 992px) and (max-width: 1199px) {
									left: 10px;
								}
								@include media-breakpoint-down(md) { /* max 991 */
									display: none;
								}
							}
							i {
								float: right;
								padding: 0;
								font-size: 15px;
								color: #414141;
								padding: 5px 0 5px 0;
							}
						}
					}
					&.parent {
						> a {
							@include media-breakpoint-up(md) { /* min 992 */
								z-index: 999;
							}
							&:after {
								@include media-breakpoint-up(md) { /* min 992 */
									content: "\E313";
									font-family: "Material Icons";
									margin-left: 3px;
									vertical-align: top;
									display: inline-block;
									float: right;
									transform:rotate(270deg);
									-webkit-transform: rotate(270deg);
									-moz-transform: rotate(270deg);
									-o-transform: rotate(270deg);
									-ms-transform: rotate(270deg);
									font-size:18px;
								}
							}	
							&:before {
								@include media-breakpoint-up(md) { /* min 992 */
									content: "\E316";
									font-family: "Material Icons";
									margin-left: 3px;
									vertical-align: top;
									display: none;
									float: right;
								}
							}
						}
						&:hover {
							> a {
								@include media-breakpoint-up(md) { /* min 992 */
									z-index: 999;
								}
							}
						}
					}
					ul {
						@include media-breakpoint-down(md) { /* max 991 */
							float: left;
							width: 100%;
							padding: 0 0 10px;
						}
						li {
							a {
								@include media-breakpoint-down(md) { /* max 991 */
									color: #666;
								}
								@include media-breakpoint-only(md) { /* only 991 */
									color: #666666;
									font-size: 14px;
									font-weight: normal;
									padding: 3px 10px;
									text-align: left;
								}
								&:hover {
									@include media-breakpoint-down(md) { /* max 991 */
										color: $gray-darker;
									}
								}
							}
							&.parent {
								> a {
									&:after {
										@include media-breakpoint-up(md) { /* min 992 */
											content: "\f054";
											bottom: 0;
											color: #b3b3b3;
											font-family: $font-icon;
											font-size: 10px;
											height: 15px;
											margin: auto;
											position: absolute;
											right: 15px;
											top: 0;
											vertical-align: middle;
											width: 10px;
										}
									}
								}
								&:hover {
									&:after {
										@include media-breakpoint-down(md) { /* max 991 */
											color: $gray-darker;
										}
									}
								}
							}
						}
					}
					&:last-child {
						&:after {
							@include media-breakpoint-up(md) { /* min 992 */
								border:none;
							}
						}
					}
					&:hover {
						> a {
							color: $brand-secondary;
							background: rgba(255, 255, 255, 0.1);
							@include media-breakpoint-down(md) { /* max 991 */
								color: $gray-darker;
							}
							&:before {
								@include media-breakpoint-down(md) { /* max 991 */
									display: none;
								}
							}
						}
						.more-menu {
							.categories {
								color: $gray-darker;
							}
						}
						.menu-dropdown {
							@include media-breakpoint-up(md) { /* min 992 */
								filter: alpha(opacity=100);
								-webkit-transform: scale(1,1);
								-moz-transform: scale(1,1);
								-ms-transform: scale(1,1);
								-o-transform: scale(1,1);
								transform: scale(1,1);
								visibility: visible;
								text-align: left;
								display: block;
								opacity: 1;

							}
						}
						.rv-sub-menu {
							/*@include media-breakpoint-up(md) {  min 992 
								display: block !important;
								}*/
							}
						}
					}
					&.level-2 {
						@include media-breakpoint-down(md) { /* max 991 */
							position: relative;
							cursor: pointer;
							width: 100%;
							float: left;
						}
						a {
							@include media-breakpoint-down(md) { /* max 991 */
								padding: 5px 0;
							}
						}
						.menu-dropdown {
							@include media-breakpoint-up(md) { /* min 992 */
								left: 100%;
								top: -1px;
								box-shadow: 0 2px 8px #e0e0e0;
								-moz-box-shadow: 0 2px 8px #e0e0e0;
								-webkit-box-shadow: 0 2px 8px #e0e0e0;
							}
						}
						&:hover {
							.menu-dropdown {
								@include media-breakpoint-up(md) { /* min 992 */
									filter: alpha(opacity=100);
									-webkit-transform: scale(1,1);
									-moz-transform: scale(1,1);
									-ms-transform: scale(1,1);
									-o-transform: scale(1,1);
									transform: scale(1,1);
									visibility: visible;
									text-align: left;
									display: block;
									opacity: 1;
								}
							}
						}
					}
					&.level-3, &.level-4, &.level-5 {
						@include media-breakpoint-down(md) { /* max 991 */
							position: relative;
							cursor: pointer;
							width: 100%;
							float: left;
						}
						a {
							@include media-breakpoint-down(md) { /* max 991 */
								padding: 5px 0;
							}
						}
					}
					&.rvblog {
						a {
							padding: 12px 20px 12px 50px;
							@media (min-width: 992px) and (max-width: 1199px) {
								padding: 12px 20px 12px 40px;
							}
							@include media-breakpoint-down(md) { /* max 991 */
								padding: 6px 20px 6px 45px;
							}
							&:before {
								bottom: auto;
								content:"\e02f";
								font-family: "Material Icons";
								font-size: 20px;
								left:20px;
								position: absolute;
								right: auto;
								top:11px;
								color:#888;
								@media (min-width: 1200px) and (max-width: 1299px) {
									left:14px;
								}
								@media (min-width: 992px) and (max-width: 1199px) {
									left:10px;
								}
								@include media-breakpoint-down(md) { /* max 991 */
									left: 15px;
									top: 5px;
								}
							}
						}
						&.level-1 {
							.dropdown-item {
								@media (min-width: 1200px) and (max-width: 1299px) {
									padding: 12px 13px 12px 40px !important;
								}
								@media (min-width: 992px) and (max-width: 1199px) {
									padding: 12px 12px 12px 40px !important;
								}
							}
						}
						&:hover {
							> a {
								:before {
									@include media-breakpoint-down(md) { /* max 991 */
										display:block;
									}
								}
							}
						}
					}
					&.more_menu {
						.rvinnermenu {
							&:after {
								@include media-breakpoint-up(md) { /* min 992 */
									display: none;
								}
							}
						}
						.menu-subtitle {
							@include media-breakpoint-up(md) { /* min 992 */
								float: left;
								margin: 0 5px;
								position: relative;
								right: 0;
								top: 0;
							}
						}
						.rv-sub-menu {
							&.menu-dropdown {
								@include media-breakpoint-up(md) { /* min 992 */
									border: medium none;
									box-shadow: none;
									opacity: 1;
									-moz-opacity: 1; 
									-khtml-opacity: 1; 
									-webkit-opacity: 1; 
									position: static;
									transform: none;
									visibility: visible;
									margin: 0 0 30px;
								}
							}
						}
						.cat-drop-menu {
							li {
								width: 100%;
								position: relative;
								@include media-breakpoint-up(md) { /* min 992 */
									width: 250px;
								}
							}
						}
						.menu-dropdown {
							@include media-breakpoint-only(md) { /* only 991 */
								display: block;
								padding: 0;
							}
							&.rv-sub-auto {
								@include media-breakpoint-up(md) { /* min 992 */
									left:0;
									right:0;
								}
							}
							.menu-dropdown {
								@include media-breakpoint-up(md) { /* min 992 */
									border: medium none;
									box-shadow: none;
									/*display: block !important;*/
									opacity: 1;
									-moz-opacity: 1; 
									-khtml-opacity: 1; 
									-webkit-opacity: 1; 
									padding: 0;
									position: static;
									transform: none;
									visibility: visible;
								}
							}
						}
						ul {
							li {
								a {
									@include media-breakpoint-only(md) { /* only 991 */
										font-size: 16px;
										padding: 6px 15px;
									}
								}
								&:last-child {
									> a {
										@include media-breakpoint-only(md) { /* only 991 */
											border: none;
										}
									}
								}
							}
						}
						a {
							&.dropdown-item {
								@include media-breakpoint-only(md) { /* only 991 */
									display: none;
								}
							}
						}
						.catagory {
							@include media-breakpoint-up(md) { /* min 992 */
								float: left;
								font-size: 16px;
								text-transform: capitalize;
							}
						}
						&.level-1 {
							> a {
								&:after {
									@include media-breakpoint-up(md) { /* min 992 */
										content: "\E313";
										font-family: "Material Icons";
										margin-left: 3px;
										vertical-align: top;
										display: inline-block;
										float: none;
									}
								}
								&:before {
									@include media-breakpoint-up(md) { /* min 992 */
										content: "\E316";
										font-family: "Material Icons";
										margin-left: 3px;
										vertical-align: top;
										display: none;
										float: right;
									}
								}
							}
							&:hover {
								> a {
									&:after {
										@include media-breakpoint-up(md) { /* min 992 */
											display: none;
										}
									}
									&:before {
										@include media-breakpoint-up(md) { /* min 992 */
											display: none;
										}
									}
								}
							}
						}
					}
					ul {
						li {
							&:last-child {
								@include media-breakpoint-down(md) { /* max 991 */
									border-bottom: medium none !important;
								}
							}
						}
					}
				}
			}  
			.menu-dropdown {
				@media (max-width: 1200px) {
					width: 650px;
				}
				@include media-breakpoint-up(md) { /* min 992 */
					opacity: 0;
					filter: alpha(opacity=0);
					transform: scale(0,1);
					-webkit-transform: scale(0,1);
					-moz-transform: scale(0,1);
					-ms-transform: scale(0,1);
					-o-transform: scale(0,1);
					transform-origin: left top 0;
					-webkit-transform-origin: left top 0;
					-moz-transform-origin: left top 0;
					-ms-transform-origin: left top 0;
					-o-transform-origin: left top 0;
					-webkit-transition: all 500ms ease 0s;
					-moz-transition: all 500ms ease 0s;
					-ms-transition: all 500ms ease 0s;
					-o-transition: all 500ms ease 0s;
					transition: all 500ms ease 0s;
					visibility: hidden;
					background: $light-color;
					border:1px solid $base-border-color;
					padding: 30px 15px;
					position: absolute;
					top: -1px;
					z-index: 1000;
					width: 750px;
					left: 100%;
					box-shadow: 0 0 0 1px rgba(0,0,0,.1);
					border-left: 3px solid $brand-secondary;
					border-radius: 5px;
				}
				@include media-breakpoint-down(md) { /* max 991 */
					display: none;
				}
				.menu-item {
					.thumbnail-container {
						border: 1px solid #e5e5e5;
						.product-description {
							text-align: center;
							padding: 20px 0;
							.product-title {
								a{
									font: 400 14px/24px $font-family;
								}
								.product-name {
									font-size: 16px;
									line-height: 20px;
									font-weight: 500;
									color: $brand-primary;
									-webkit-transition: all 0.3s ease 0s;
									-moz-transition: all 0.3s ease 0s;
									-ms-transition: all 0.3s ease 0s;
									-o-transition: all 0.3s ease 0s;
									transition: all 0.3s ease 0s;
									&:hover {
										color:$brand-secondary;
									}
								}
							} 
							.content_price {
								.price {
									color: var(--rv-brand-primary);
									font-weight: 700;
								}
								.discount-percentage {
									display: inline;
									background: #d9d9d9;
									font-weight: normal;
									line-height: 10px;
									margin: 0 5px;
									padding: 1px;
									color: $gray-darker;
									font-size: 12px;
								}
								.old-price {
									color: #aaa;
									text-decoration: line-through;
								}
							}
						} 
						.rvproduct-image {
							display: inline-block;
							float: none;
							vertical-align: top;
							border-radius:8px;
							-moz-border-radius:8px;
							-webkit-border-radius:8px;
							-khtml-border-radius:8px;
							.productcountdown {
								display: none!important;
							}
						}
						&.clearfix {
							margin: 0;
							@include media-breakpoint-only(md) { /* only 991 */
								display: table;
								float: none;
								margin: 0 auto;
								width: auto;
							}
						}
					}
				}
				ul {
					li {
						&:first-child {
							> a {
								border-top: none;
							}
						}
						&.product-block {
							@include media-breakpoint-up(md) { /* min 992 */
								float: left;
								padding: 0;
								width: 100%;
							}
							.products {
								@include media-breakpoint-up(md) { /* min 992 */
									padding:5px !important;
								}
							}
						}
					}
				}
				&.rv-sub-left {
					@media (max-width: 1499px) {
						width : 830px!important;
					}
					@media (max-width: 1199px) {
						width : 740px!important;
					} 
					@media (min-width: 1200px) and (max-width: 1299px) {
						width: 870px!important;
					}
					@media (max-width: 1200px) {
						width: 650px;
					}
					@include media-breakpoint-up(md) { /* min 992 */
						width: 1130px;
					}
				}
				&.rv-sub-right {
					@media (max-width: 1200px) {
						width: 780px;
					}
					@media (min-width: 992px) and (max-width: 1199px) {
