#products,
#pagenotfound {
  #main {
    .page-header {
      margin: rem(32px) 0 rem(48px);
    }
    .page-content {
      margin-bottom: rem(160px);
    }
  }
}
.page-not-found {
  border: 1px solid $base-border-color;
  background: white;
  padding: rem(16px);
  font-size: $font-size-sm;
  color: $gray-dark;
  margin: 0 auto;
  h4 {
    font-size: $base-font-size;
    font-weight: 700;
    margin: rem(8px) 0 rem(16px);
  }
  .search-widget {
    float: none;
    padding: 0;
    .searchtoggle {
      display: none;
    }
    form {
      position: relative;
      display: block;
      top: auto;
      right: auto;
    }
    input {
      width: 100%;
    }
  }
}



