#rvimageslider {
	position: relative;
	overflow: hidden;
	z-index: 1;
	margin-top: 30px;
	#slider {
		.nivo-directionNav {
			.nivo-prevNav {
				cursor: url(../../assets/img/prev.png), pointer;
				left: 0;
				right: auto;
				@include media-breakpoint-down(sm) { /* max 767*/
					display: none;
				}
				i {
					display: none;
				}
			}
			.nivo-nextNav {
				cursor: url(../../assets/img/next.png), pointer;
				left: auto;
				right: 38px;
				@include media-breakpoint-down(sm) { /* max 767*/
					display: none;
				}
				i {
					display: none;
				}
			}
			a {
				display: block;
				font-size: 0;
				height: 100%;
				margin: 0;
				overflow: hidden;
				position: absolute;
				text-decoration: none;
				top: 0;
				width: 175px;
				z-index: 0;
				&:hover {
					background: $light-color;
					color: $brand-primary;
				}
				i {
					font-size: 16px;
				}
			}
		}
	}
	&:hover {
		#slider {
			.nivo-directionNav {
				a {
					z-index: 7;
					background: transparent;
				}
			}
		}
	}
	.nivo-controlNav {
		bottom: 0px;
		display: block;
		left: 0;
		position: absolute;
		right: 0;
		text-align: center;
		z-index: 6;
		padding: 0;
		.nivo-control {
			background: $brand-primary;
			border: none;
			cursor: pointer;
			display: inline-block;
			font-size: 0;
			height: 15px;
			margin: 8px 4px;
			transition: all 0.3s ease 0s;
			width: 15px;
			border-radius: 50px;
			-webkit-transition: all 0.3s ease;
			@media (max-width:540px) and (min-width:320px) {
				height: 12px;
				width: 12px;
			}
			&.active {
				background: $brand-secondary;
				border: none;
			}
			&:hover, &:focus, &:active {
				background: $brand-secondary;
				border: none;
			}
		}
	}
}



#spinner {
	background: $light-color url(../../assets/img/loader.gif) no-repeat scroll 50% 50%;
	position: fixed;
	left: 0px;
	top: 0px;
	width: 100%;
	height: 100%;
	z-index: 1001;
}


