#products {
  .products-select {
    @include display(flex);
    @include align-items(baseline);
    @include justify-content(space-between);
  }
}

.block-category {
  background: transparent none repeat scroll 0 0;
  border: medium none;
  box-shadow: none;
  margin: 0px 0 20px;
  padding: 0;
  h1 {
    margin-bottom: rem(15px);
    text-transform: uppercase;
    font-weight: 500;
  }
  .category-cover {
    margin-bottom: 20px;
    img {
      max-width: 100%;
      border-radius: 5px;
    }
  }
  #category-description {
    p,
    strong {
      font-weight: bold;
      color: $gray-darker;
    }
    p {
      color: $gray;
      margin-bottom: 5px;
      font-weight: 400;
    }
  }
}

#subcategories {
  margin-bottom: 15px;
  .subcategory-heading {
    border-bottom: 2px solid $brand-primary;
    font: 700 18px / 24px $font-family;
    color: $base-heading-color;
    margin: 0;
    padding: rem(8px) 0;
    position: relative;
    text-align: left;
    text-transform: uppercase;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      height: 2px;
      width: 150px;
      background: $brand-secondary;
      transition: all 0.3s ease-out 0s;
      -moz-transition: all 0.3s ease-in-out 0s;
      -webkit-transition: all 0.3s ease-in-out 0s;
      -khtml-transition: all 0.3s ease-in-out 0s;
      -o-transition: all 0.3s ease-in-out 0s;
    }
  }
  #subcategory-carousel {
    .item {
      padding: 15px;
      .subcategory-container {
        text-align: center;
        &:hover {
          .subcategory-image {
            border-color: $brand-primary;
          }
        }
        .subcategory-image {
          border: 1px solid $base-border-color;
          margin: 0 0 10px 0;
          border-radius: 5px; 
          -webkit-transition: all 0.3s ease 0s;
          -moz-transition: all 0.3s ease 0s;
          -ms-transition: all 0.3s ease 0s;
          -o-transition: all 0.3s ease 0s;
          transition: all 0.3s ease 0s;
          padding: 5px;
        }
        .subcategory-content {
          overflow: hidden;
        }
      }
    }
    .owl-nav{
      margin: 0;
      position: absolute;
      right: 15px;
      top: -35px;
      .owl-prev {
        i {
          &:before {
            content: "\f104";
          }
        }
      }
      .owl-next {
        i {
          &:before {
            content: "\f105";
          }
        }
      }
      > div {
        width:30px;
        height:30px;
        padding: 5px;
        margin: 0 0 0 5px;
        text-align: center;
        border:1px solid $base-border-color;
        background: $light-color;
        -webkit-transition: all 0.3s ease 0s;
        -moz-transition: all 0.3s ease 0s;
        -ms-transition: all 0.3s ease 0s;
        -o-transition: all 0.3s ease 0s;
        transition: all 0.3s ease 0s;
        font-size: 18px;
        display: inline-block;
        &.disabled {
          opacity: 0.4;
        }
        &:hover {
          -webkit-transition: all 0.3s ease 0s;
          -moz-transition: all 0.3s ease 0s;
          -ms-transition: all 0.3s ease 0s;
          -o-transition: all 0.3s ease 0s;
          transition: all 0.3s ease 0s;
          color: $link-hover-color;
          border-color: $link-hover-color;
        }
      }
    }
  }
}

#product-list-top {
  padding: rem(15px);
  background: $light-color;
  margin-bottom: rem(20px);
  border-radius: 5px; 
}
.products-selection {
  .sort-by {
    padding-top: rem(12px);
    white-space: nowrap;
    text-align: right
  }
  .total-products {
    padding-top: 6px;
    @include media-breakpoint-down(md) { /* max 991 */
      padding-top: 0;
      padding-bottom: 10px;
      display: inline-block;
      width: 100%;
    }
    p {
      margin-bottom: 0;
      padding: 5px 0 0;
      vertical-align: middle;
      @include media-breakpoint-down(md) { /* max 991 */
        float: right;
      }
    }
    .display {
      display: inline-block;
      float: left;
      margin: 0 10px;
      vertical-align: middle;
      li {  
        display: inline-block;
        text-align: center;
        vertical-align: middle;
        &#grid 
        { 
          a {
            background:url("../img/grid.png") no-repeat scroll 5px 5px transparent;
            &:hover{
              background-position: 5px -28px;
            }
          }
        }
        &#grid.selected > a{
          background-position: 5px -28px;
        }
        &#list 
        { 
          a {
            background:url("../img/grid.png") no-repeat scroll -32px 5px transparent;
            &:hover{
              background-position: -32px -28px;
            }
          }
        }
        &#list.selected > a{
          background-position: -32px -28px;
        }
        a {
          color: $gray;
          font-size: 14px;
          line-height: 14px;
          cursor: pointer;
          height: 28px;
          width: 28px;
          display: block;
          -webkit-transition: all 0.3s ease 0s;
          -moz-transition: all 0.3s ease 0s;
          -ms-transition: all 0.3s ease 0s;
          -o-transition: all 0.3s ease 0s;
          transition: all 0.3s ease 0s;
          &:hover {
            color: $brand-primary;
            -webkit-transition: all 0.3s ease 0s;
            -moz-transition: all 0.3s ease 0s;
            -ms-transition: all 0.3s ease 0s;
            -o-transition: all 0.3s ease 0s;
            transition: all 0.3s ease 0s;
          }
        }
      }
    }
  }
  h1 {
    padding-top: rem(10px);
  }
  .products-sort-order {
    color: $gray-dark;
    .products-sort-order-inner {
      position: relative;
    }
    .select-title {
      display: inline-block;
      width: 100%;
      color: $gray-darker;
      background: white;
      padding: rem(9px);
      border: 1px solid $base-border-color;
      cursor: pointer;
      border-radius: 5px; 
      i {
        font-size: rem(18px);
      }
    }
    .select-list {
      display: block;
      color: $gray-darker;
      padding: $small-space;
      &:hover {
        background: $brand-primary;
        color: white;
        text-decoration: none;
      }
    }
    .dropdown-menu {
      left: auto;
      width: 100%;
      background: $gray-lighter;
      border: none;
      border-radius: 5px;
      box-shadow: 0 0 0 4px #e0e0e0 inset;
      padding: rem(10px);
      min-width: 0;
    }
  }
}
#search_filters {
  background: $light-color none repeat scroll 0 0;
  padding: 15px;
  border: none;
  border-radius: 10px; 
  > h4 {
    color: $brand-primary;
    font: 500 16px/16px $font-custom;
    position: relative;
    margin-bottom: 0;
    text-align: left;
    display: block;
    vertical-align: middle;
    text-transform: uppercase;
    padding: 0 0 10px;
    border-bottom: 1px solid #eee;
    margin-top: 0;
    &:before {
      border-bottom: 2px solid $brand-secondary;
      bottom: 0px;
      content: "";
      left: 0;
      position: absolute;
      width: 110px;
      display: inline-block;
    }
  }
  .filter_block_content {
  }
  .facet {
    padding-top: 10px;
    ul {
      margin-bottom: 0;
    }
    .collapse {
      display: block;
      @include media-breakpoint-up(lg) { /* min 992 */
        height: auto !important;
      }
    }
    .facet-title {
      color: $gray;
      text-transform: uppercase;
    }
    .facet-label {
      padding: rem(5px) 0;
      margin: 0;
      a {
        color: $gray;
        display: inline-block;
        vertical-align: middle;
        &:hover {
          color: $brand-secondary;
        }
      }
    }
  }
}
.pagination {
  width: 100%;
  padding: 0 15px;
  .product-count {
    line-height: 35px;
    float: left;
    @include media-breakpoint-down(sm) { /* max 767 */
      text-align: center;
      width: 100%;
    }
  }
  .pagination_bottom {
    float: right;
    @include media-breakpoint-down(sm) { /* max 767 */
      width: 100%;
    }
    .page-list {
      margin-bottom: 0;
      li {
        display: inline-block;
        height: rem(35px);
        width: rem(35px);
        vertical-align: middle;
        border: 1px solid $base-border-color;
        a {
          color: black;
          font-weight: 600;
          line-height: 33px;
          cursor: pointer;
          display: block;
          &.disabled {
            color: $gray;
            cursor: not-allowed;
          }
          &.previous, &.next {
            font-size: 0;
            i {
              line-height: 33px;
              font-size: 20px;
            }
          }

        }
        &.current a {
          cursor: default;
        }
        &.current, &:hover {
          border-color: $brand-primary;
          a {
            color: $brand-primary;
            text-decoration: none;
          }
        }
      }
    }
  }  
}
.active_filters {
  background: #dededd;
  padding: $small-space $large-space 0;
  margin-bottom: $medium-space;
  .active-filter-title {
    display: inline;
    margin-right: $small-space;
    font-weight: 600;
    text-transform: uppercase;
  }
  ul {
    display: inline;
  }
  .filter-block {
    @include box-shadow;
    color: $gray-darker;
    margin-right: $small-space;
    margin-bottom: $small-space;
    background: white;
    padding: rem(10px);
    display: inline-block;
    font-size: $font-size-xs;
    > a {
    	display: inline-block;
    	vertical-align: middle;
    }
    .close {
      color: $gray-darker;
      font-size: $font-size-lg;
      opacity: 1;
      margin-left: rem(5px);
    }
  }
}

.block-categories {
  margin-bottom: rem(25px);
  h2.title_block {
    display: block;
    padding: 0 0 rem(10px);
    margin: 0;
    color: $base-heading-color;
    font-size: 15px;
  }
  .category-sub-menu {
    li {
      position: relative;
      > a {
        display: block;
        padding: 5px 15px;
        color: $brand-primary;
        &:hover {
          color: $brand-secondary;
        }
      }
      .category-sub-menu {
        padding: 0 0 0 rem(15px);
        li {
          a {
            color: $gray;
            &:hover {
              color: $brand-primary;
            }
          }
        }
      }
    }
    .collapse-icons {
      position: absolute;
      right: 0;
      top: 5px;
      padding: 0;
      cursor: pointer;
      border: medium none;
      &[aria-expanded="true"] {
        .add {
          display: none;
        }
        .remove {
          display: block;
        }
      }
      .add,
      .remove {
        font-size: rem(18px);
        color: $gray;
        &:hover {
          color: $brand-primary;
        }
      }
      .remove {
        display: none;
      }
    }
  }
}
.facets-title {
  color: $base-heading-color;
}
.products-selection {
  .filter-button {
    .btn-secondary {
      padding: rem(8px) rem(8px);
    }
  }
}

/*** Responsive part ***/
@include media-breakpoint-down(md) {
  #category {
    #left-column {
      #search_filter_controls {
        text-align: center;
        margin-bottom: rem(16px);
        button {
          margin: 0 rem(8px);
        }
      }
      #search_filters {
        margin-bottom: 0;
        padding: 0;
        .facet {
          .title {
            cursor: pointer;
            position: relative;
            .collapse-icons .remove {
              display: none;
            }
          }
          .title[aria-expanded="true"] {
            .collapse-icons {
              .add {
                display: none;
              }
              .remove {
                display: block;
              }
            }
          }
          .facet-title {
            margin-bottom: 0;
            padding: rem(10px) 0;
            display: inline-block;
          }
          .navbar-toggler {
            display: inline-block;
            font-size: 20px;
            height: 30px;
            padding: 2px 0;
            position: absolute;
            right: 0;
            text-align: center;
            top: 6px;
          }
          .collapse {
            display: none;
            &.in {
              display: block;
            }
          }
        }
      }
    }
    #search_filter_toggler {
      width: 100%;
    }
  }
  .products-selection {
    h1 {
      padding-top: 0;
      text-align: center;
      margin-bottom: rem(16px);
    }
    .showing {
      padding-top: rem(16px);
    }
  }
}


