.star {
  cursor: pointer;
  float: left;
  font-size: 14px;
  font-weight: 400;
  height: 20px;
  overflow: hidden;
  position: relative;
  width: 15px;
  display: block;
  a {
   display: block;
   font-size: 11px;
   position: absolute;
   text-indent: -5000px;
 }
}
div {
  &.star {
    display: inline-block;
    float: none;
    margin: 0;
    vertical-align: top;
    &:after {
      color: $brand-primary;
      content: "\f006";
      display: inline-block;
      font-family: "FontAwesome";
    }
    &.star_on {
      &:after {
        color: $brand-primary;
        content: "\f005";
        display: inline-block;
        font-family: "FontAwesome";
      }
    }
    &.star_hover {
      &:after {
        color: $brand-primary;
        content: "\f005";
        display: inline-block;
        font-family: "FontAwesome";
      }
    }
  }
}

