#rvdealoftheday {
	margin-bottom: 30px;
	margin-top: 0;
	padding: 0;
	width: 100%; 
	@include media-breakpoint-down(md) { /* max 991 */
		margin-bottom: 0;
	}
	@include media-breakpoint-down(xs) { /* max 543 */
		margin-bottom: 30px;
	}	
	.products_block_inner {
		text-align: center;
		position: relative;
		width: 100%;
		overflow: hidden;
		.rv-titletab {
			display: none;
		}
		.dealofdayimage {
			.item {
				.product-container {
					position: relative;
					text-align: center;
					overflow: hidden;
					background-color: $light-color;
					border-radius: 10px;
					@include media-breakpoint-down(md) { /* max 991 */
						margin: 0 0 30px;
					}
					.thumbnail-container {
						position: relative;
						@media (min-width: 320px) and (max-width: 440px) {
							padding: 0 15px;
						}
						.thumbnail-inner {
							position: relative;
							overflow: hidden; 
							padding: 15px 0 15px 15px;
							@include media-breakpoint-down(sm) { /* max 767*/
								padding: 15px;
							}
							@include media-breakpoint-down(xs) { /* max 543 */
								padding: 40px 40px 15px;
							}
							.product-buttons {
								cursor: pointer;
								display: inline-block;
								left: 15px;
								right: 0;
								position: absolute;
								text-align: center;
								bottom: 50px;
								overflow: hidden;
								.btn-tertiary, .btn-secondary, .btn-primary {
									height: 35px;
									padding: 0;
									transition: all 0.5s ease 0s;
									width: 35px;
									border-radius: 50%;
									border: 1px solid $brand-primary;

								} 
								> div {
									display: inline-block;
									opacity: 0;
									transform: translateY(60px);
									vertical-align: middle;
									transition: all 0.3s ease 0s;
									-webkit-transition: all 0.3s ease 0s;
									-moz-transition: all 0.3s ease 0s;
									-ms-transition: all 0.3s ease 0s;
									-o-transition: all 0.3s ease 0s;
									&:nth-child(1) {
										transition-delay: 0.3s;
									}
									&:nth-child(2) {
										transition-delay: 0.4s;
									}
									&:nth-child(3) {
										transition-delay: 0.5s;
									}
									&:nth-child(4) {
										transition-delay: 0.6s;
									}
								}
								.wishlist_btn {
									.btn {
										background-image: url(../../assets/img/sprite.png);
										background-color: $brand-primary;
										background-position: -68px -476px;
										&:hover {
											background-color: $brand-secondary;
											background-position: -120px -476px;
											border: 1px solid $brand-secondary;

										}
										span {
											display: none;
										}
										i {
											display: none;
										}
									}
								}
								.product-cart-btn {
									.btn {
										background-image: url(../../assets/img/sprite.png);
										background-color: $brand-primary;
										background-position: -69px -524px;
										&:hover {
											background-color: $brand-secondary;
											background-position: -120px -524px;
											border: 1px solid $brand-secondary;
										}
										i{
											display: none;
										}
										span {
											display: none;
										}
									}
								}
								.product-quick-btn {
									.btn {
										background-image: url(../../assets/img/sprite.png);
										background-color: $brand-primary;
										background-position: -69px -431px;
										&:hover {
											background-color: $brand-secondary;
											background-position: -121px -431px;
											border: 1px solid $brand-secondary;
										}
										i{
											display: none;
										}
										.lblquickview {
											display: none;
										}
									}
								}
								.compare {
									.btn {
										background-image: url(../../assets/img/sprite.png);
										background-color: $brand-primary;
										background-position: -69px -571px;
										&:hover {
											background-color: $brand-secondary;
											background-position: -121px -571px;
											border: 1px solid $brand-secondary;
										}
									}
									span {
										display: none;
									}
									i {
										display: none;
									}
								}
							}
						}
						.product-thumbnail {
							display: block;
							text-align: center;
							position: relative;
							border: 1px solid $base-border-color; 
							img {
								margin: 0 auto;
								max-width: 100%;
								-webkit-transition: all 0.7s ease 0s;
								-moz-transition: all 0.7s ease 0s;
								-ms-transition: all 0.7s ease 0s;
								-o-transition: all 0.7s ease 0s;
								transition: all 0.7s ease 0s;
								&.img_1 {
									left: 0;
									position: absolute;
									right: 0;
									top: 0;
									opacity: 0;
									-moz-transform: scale(0,1);
									-webkit-transform: scale(0,1);
									-o-transform: scale(0,1);
									-ms-transform: scale(0,1);
									transform: scale(0,1);
								}
							}
							&:after{
								content: "";
								position: absolute;
								background: rgba(255,255,255,.1);
								top: 0;
								left: 0;
								right: 0;
								bottom: 0;
								transform: scale(0);
								-moz-transform: scale(0);
								-webkit-transform: scale(0);
								-o-transform: scale(0);
								-ms-transform: scale(0);
								transition: all 0.5s;
								-webkit-transition: all 0.5s;
								-moz-transition: all 0.5s;
								-o-transition: all 0.5s;
							}
						}
						.product_list_thumb {
							display: none;
							.product-images {
								margin: 0;
								.thumb-container {
									margin: 0 5px;
									> a {
										display: block;
										img {
										}
										&.selected {
											img {
												border-color: $gray-darker;
											}
										}
									}
								}
								.owl-nav{
									margin: 0;
									position: absolute;
									right: 0;
									top: 50%;
									left: 0;
									display: none;
									.owl-prev {
										left: -30px;
										i {
											&:before {
												content: "\E5CB";
											}
										}
										&:after {
											display: none;
										}
										&:before {
											display: none;
										}
									}
									.owl-next {
										right: -23px;
										left: auto;
										i {
											&:before {
												content: "\E5CC";
											}
										}
										&:after {
											display: none;
										}
										&:before {
											display: none;
										}
									}
									> div {
										width:30px;
										height:30px;
										padding: 2px;
										margin: 0;
										border: medium none;
										background: transparent;
										-webkit-transition: all 0.3s ease 0s;
										-moz-transition: all 0.3s ease 0s;
										-ms-transition: all 0.3s ease 0s;
										-o-transition: all 0.3s ease 0s;
										transition: all 0.3s ease 0s;
										font-size: 0;
										position: absolute;
										margin-top: -15px;
										color: $brand-primary;
										&:hover {
											-webkit-transition: all 0.3s ease 0s;
											-moz-transition: all 0.3s ease 0s;
											-ms-transition: all 0.3s ease 0s;
											-o-transition: all 0.3s ease 0s;
											transition: all 0.3s ease 0s;
											color: $gray-darker;
											border: none;
										}
									}
								}
							}
						}
						.product-flags {
							display: none;
							> li {
								font-size: 12px;
								color: $brand-primary;
								font-weight: 500;
								opacity: 0;
								position: absolute;
								text-transform: uppercase;
								-webkit-transition: all 0.7s ease 0s;
								-moz-transition: all 0.7s ease 0s;
								-ms-transition: all 0.7s ease 0s;
								-o-transition: all 0.7s ease 0s;
								transition: all 0.7s ease 0s;
							}
							.discount, .on-sale{
								right: 10px;
								top: 30px !important;
								left: auto;
								transform: translateX(100%);
								-webkit-transition: all 0.7s ease 0s;
								-moz-transition: all 0.7s ease 0s;
								-ms-transition: all 0.7s ease 0s;
								-o-transition: all 0.7s ease 0s;
								transition: all 0.7s ease 0s;
							}
							.new {
								right: 10px;
								top: 10px !important;
								left: auto;
								color: $gray-dark-color;
								transform: translateX(100%);
								-webkit-transition: all 0.7s ease 0s;
								-moz-transition: all 0.7s ease 0s;
								-ms-transition: all 0.7s ease 0s;
								-o-transition: all 0.7s ease 0s;
								transition: all 0.7s ease 0s;
							}
							.online-only {
								display: none;
								left: 10px;
								bottom: 10px;
							}
							.pack {
								right: 10px;
								top: 30px;
							}
						}
					}
					.product-description {
						text-align: left;
						padding: 30px 10px 30px 0;
						@include media-breakpoint-down(xs) { /* max 543 */
							text-align: center;
							padding: 15px 15px 20px;
						}
						.product-title {
							margin-bottom: rem(15px);
							@include media-breakpoint-only(md) { /* max 991 */
								margin-bottom: 10px;
							}
							@include media-breakpoint-down(xs) { /* max 543 */
								margin-bottom: 5px;
							}
							a {
								color: $brand-primary;
								font-size: 24px;
								display: inline-block;
								border-bottom: none;
								text-decoration: none;
								text-align: center;
								font-weight: 500;
								text-transform: capitalize;
								letter-spacing: 0.5px;
								font-family: $font-custom;
								transition: all 0.3s ease 0s;
								-webkit-transition: all 0.3s ease 0s;
								-moz-transition: all 0.3s ease 0s;
								-ms-transition: all 0.3s ease 0s;
								-o-transition: all 0.3s ease 0s;
								@include media-breakpoint-only(md) { /* max 991 */
									font-size: 20px;
								}
								@include media-breakpoint-down(sm) { /* max 767 */
									padding: 0;
									font-size: 24px;
								}
								&:hover {
									color: $brand-secondary;
									transition: all 0.3s ease 0s;
									-webkit-transition: all 0.3s ease 0s;
									-moz-transition: all 0.3s ease 0s;
									-ms-transition: all 0.3s ease 0s;
									-o-transition: all 0.3s ease 0s;
								}
							}
						}
						.product-price-and-shipping {
							color: $gray-light;
							font-weight: 500;
							margin-bottom: rem(15px);
							font-size: 16px;
							> span {
								display: inline-block;
								vertical-align: middle;
							}
							.regular-price {
								color: $gray-dark;
								text-decoration: line-through;
							}
							.discount-percentage {
								margin: 0 rem(5px);
								padding: rem(5px);
								color: $brand-primary;
								font-weight: 600;
								background: #d9d9d9;
								font-weight: normal;
								line-height: 10px;
								padding: 4px 5px;
								color: $gray-darker;
								font-size: 14px;
							}
							.price {
								color: $brand-primary;
								font-weight: 700;
							}
						}
						.product-desc {
							color: $gray;
							padding: 0 10px 0 0;
							font-size: 13px;
							font-family: $font-family;
							@include media-breakpoint-down(sm) { /* max 767 */
								width: 90%;
								padding: 0;
							}
							@include media-breakpoint-down(xs) { /* max 543 */
								padding: 0 25px;
								width: auto;
							}
							@media (max-width:540px) and (min-width:320px) {
								padding: 0 15px;
								width: auto;
								margin-bottom: 8px;
							}
						}
						.variant-links {
							margin-bottom: rem(15px);
							position: relative;
							width: 100%;
							display: none;
							.count {
								color: $gray-dark;
								font-weight: 700;
								position: relative;
								vertical-align: middle;
							}
						}
						.comments_note {
							margin-bottom: 0.9375rem;
							@include media-breakpoint-down(xs) { /* max 543 */
								margin-bottom:5px; 
							}
							.star {
								&:after {
									color:$brand-secondary;
								}
							}
						}
					}
					.dealofday_title{
						color:$brand-primary;
						font: 500 16px/20px $font-custom;
						margin-left: 5px;
						margin-bottom: 6px;
						@media (min-width: 992px) and (max-width: 1366px) {
							margin-bottom: 8px;
						}
						@media (max-width:540px) and (min-width:320px) {
							margin-bottom: 10px;
						}
					}
					.dealofday {
						position: relative;
						.dealtitle {
							font-weight: bold;
							color: $base-heading-color;
							display: inline-block;
							margin-bottom: 10px;
							font-family: $font-family;
						}
						@media (min-width: 320px) and (max-width: 440px) {
							bottom: 10px;
						}
						.timer {
							.counter {
								display: inline-block;
								position: relative;
								text-align: center;
								margin-right: 5px;
								transition: all 0.5s ease 0s;
								-webkit-transition: all 0.5s ease 0s;
								-moz-transition: all 0.5s ease 0s;
								-ms-transition: all 0.5s ease 0s;
								-o-transition: all 0.5s ease 0s;
								&:before {
									background: #ddd;
									content: "";
									height: 60px;
									position: absolute;
									top: 8px;
									width: 1px;
									margin-left: 28px;
								}
								&:nth-last-child(1) {
									&:before {
										display: none;
									}
								}
								span {
									display: block;
									text-align: center;
								}
								.date{
									width: 40px;
									height: 40px;
									background-color: $brand-secondary;
									border:1px solid $brand-secondary;
									border-radius: 50px;
									display: block;
									margin: 5px;
									@media (min-width: 992px) and (max-width: 1366px) {
										width: 35px;
										height: 35px;
									}
									.number {
										font-family: $font-family;
										color: $brand-primary;
										font-size: 16px;
										font-weight: 600;
										text-align: center;
										padding: 10px 0; 
										cursor: pointer;
										transition: all 0.5s ease 0s;
										-webkit-transition: all 0.5s ease 0s;
										-moz-transition: all 0.5s ease 0s;
										-ms-transition: all 0.5s ease 0s;
										-o-transition: all 0.5s ease 0s; 
										@media (min-width: 992px) and (max-width: 1366px) {
											padding: 7px 0;
										}
										@media (min-width: 320px) and (max-width: 440px) {
											padding: 10px;
											font-size: 15px;
										}
									}
								}
								.text {
									font-family: $font-family;
									color: $brand-primary;
									display: block;
									font-size: 15px;
									text-align: center;
									text-transform: capitalize;
									font-weight: 600;
									padding: 5px;
									cursor: pointer; 
									transition: all 0.5s ease 0s;
									-webkit-transition: all 0.5s ease 0s;
									-moz-transition: all 0.5s ease 0s;
									-ms-transition: all 0.5s ease 0s;
									-o-transition: all 0.5s ease 0s;
									position: relative;
									@media (min-width: 992px) and (max-width: 1366px) {
										font-size: 14px;
										padding: 2px;
									}
									@media (min-width: 320px) and (max-width: 440px) {
										padding: 5px 6px;
										font-size: 15px;
									}
								}
								&:hover {

									.number { 

									}
									.text { 

									}
								}
							}
						}
					}
					&:hover {
						.thumbnail-container {
							.product-thumbnail {
								img {
									-webkit-transition: all 0.7s ease 0s;
									-moz-transition: all 0.7s ease 0s;
									-ms-transition: all 0.7s ease 0s;
									-o-transition: all 0.7s ease 0s;
									transition: all 0.7s ease 0s;
									&.img_1 {
										opacity: 1;
										-moz-transform: scale(1,1);
										-webkit-transform: scale(1,1);
										-o-transform: scale(1,1);
										-ms-transform: scale(1,1);
										transform: scale(1,1);
									}
									&:after{
										transform: scale(1);
										-moz-transform: scale(1);
										-webkit-transform: scale(1);
										-o-transform: scale(1);
										-ms-transform: scale(1)
									}
								}
							}
							.thumbnail-inner {
								.product-buttons {
									> div {
										opacity: 1;
										transform: scale(1, 1);
									}
								}
							}
							.product-flags {
								> li {
									opacity: 1;
								}
								.discount, .on-sale, .new {
									transform: translateX(0);
								}
							}
							.product-description {
								.product-buttons {
									> div {
										opacity: 1;
										transform: translateX(0px);
									}
								}
							}
						}
					}
				}
			}
		}
		.owl-nav {
			display: none;
		}
	}
}


