body,
html {
  height: 100%;
}
body {
  font-family: $font-family;
  font-weight: 400;
  font-size: $base-font-size;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $gray;
  line-height: 1.25em;
}
img {
  max-width: 100%;
}
ul {
  list-style: none;
  padding-left: 0;
}
a:hover, a:focus, a:active, a.active {
  text-decoration: none;
  outline: none !important;
}
a, button {
  outline: none !important;
}
a {
  color: $gray;
  &:hover {
  color: $brand-primary;
  }
}
p {
  font-size: 15px;
  color: $gray-dark;
}
.dropdown-item:focus,
.dropdown-item:hover {
  background: none;
}
.color,
.custom-checkbox input[type="checkbox"] + span.color {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin: 0 2px;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(0,0,0,0.05);
  cursor: pointer;
  background-size: contain;
  vertical-align: middle;
  &.active,
  &:hover {
    border: 1px solid $gray-darker;
  }
}
.facet-label {
  &.active,
  &:hover {
    .custom-checkbox span.color {
      border: 1px solid $gray-darker;
    }
  }
}
.h1,
.h2,
.h3 {
  color: $gray-darker;
}
.h4 {
  font-weight: $headings-font-weight;
  color: $gray-darker;
}
.btn-primary {
  background: $button-bkg-color;
  border: 1px solid $button-bkg-color;
  color: $button-text-color;
  text-transform: uppercase;
  padding: 6px 12px;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -ms-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  &:hover, &:disabled:hover {
    background: $button-bkg-hover-color;
    color: $button-text-hover-color;
    border-color: $button-bkg-hover-color;
  }
  .material-icons {
    margin-right: rem(5px);
  }
}
.btn-secondary {
  @extend .btn-primary;
  background:$button-bkg-hover-color;
  color: $button-text-hover-color;
  border: 1px solid $button-bkg-hover-color;
  &:hover {
    background: $button-bkg-color;
    border-color: $button-bkg-color;
    color: $button-text-color;
  }
}
.btn-tertiary {
  @extend .btn-secondary;
  text-transform: capitalize;
  margin: 4px 0;
  font-weight: 400;
}
.btn-unstyle {
  background-color: transparent;
  border: none;
  padding: 0;
  text-align: inherit;
  &:focus {
    outline: 0;
    .expand-more {
      color: $brand-primary;
    }
  }
}
.card {
}
label ,.label {
  color: $gray-darker;
  text-align: right;
  font-size: rem(14px);
}
small.label, small.value {
  font-size: rem(13px);
}
.form-control-label {
  padding-top: 10px;
}
.form-control {
  background: $light-color;
  color: $gray;
  border: 1px solid $base-border-color;
  padding: 8px 12px;
  font-size: 14px;
  line-height: 16px;
  &:focus {
    border-color: $brand-primary;
  }
}
.input-group {
  &.focus {
  }
  .form-control:focus {
    outline: none;
  }
  .input-group-btn > .btn {
      border: 0;
      box-shadow: none;
      color: #ffffff;
    font-size: 11px;
      font-weight: normal;
      margin-left: 0;
    padding: 10px 16px;
      text-transform: uppercase;
  }
}
.form-control-select {
  height: rem(42px);
  -moz-appearance: none;
  -webkit-appearance: none;
  background: $light-color url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwCAQAAAD9CzEMAAAAPklEQVR4Ae3TwREAEBQD0V/6do4SXPZg7EsBhsQ8IEmSMOsiuEfg3gL3oXC7wK0bd1G4o8X9F4yIkyQfSrIByQBjp7QuND8AAAAASUVORK5CYII=") no-repeat scroll right rem(8px) center / rem(20px) rem(20px);
  padding-right: rem(32px);
}
.form-control-valign {
  padding-top: rem(8px);
}
.form-control-comment {
  font-size: 14px;
  padding-top: 8px;
  display: inline-block;
  color: $gray-dark;
}
.form-control-submit {
  &.disabled {
    background: $brand-info;
    color: white;
  }
}
.form-group {
  &.has-error {
    input,
    select {
      outline: rem(3px) solid $brand-danger;
    }
    .help-block {
      color: $brand-danger;
    }
  }
}
.group-span-filestyle {
  label {
    margin: 0;
  }
  .btn-default {
    background: $brand-primary;
    color: white;
    text-transform: uppercase;
    border-radius: 0;
    font-size: rem(14px);
    padding: rem(8px) rem(16px);
  }
}
.bootstrap-touchspin {
  input {
    &:focus {
      outline: none;
    }
    &.form-control {
      border: $input-btn-border-width solid $input-border-color;
    }
  }
  .btn-touchspin {
    @extend .btn-default;
    background-color: white;
    border: $input-btn-border-width solid $input-border-color;
    height: ($input-height / 2) + rem($input-btn-border-width);
    &:hover {
      background-color: $gray-light;
    }
  }
  .input-group-btn-vertical {
    color: $gray-darker;
    .bootstrap-touchspin-up {
      border-radius: 0;
    }
    .bootstrap-touchspin-down {
      border-radius: 0;
    }
    .touchspin-up {
      &:after {
        content: "\E5CE";
      }
    }
    .touchspin-down {
      &:after {
        content: "\E5CF";
      }
    }
    i {
      top: rem(1px);
      left: rem(3px);
      font-size: rem(15px);
    }
  }
}
.custom-radio {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid $base-border-color;
  background: $light-color;
  margin-right: rem(20px);
  input[type="radio"] {
    opacity: 0;
    cursor: pointer;
  }
  input[type="radio"]:checked + span {
      display: block;
    background-color: $gray-darker;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      position: absolute;
    left: rem(3px);
    top: rem(3px);
    }
    }
.custom-checkbox {
  position: relative;
  input[type="radio"] {
    + span {
      border-radius: 50%;
  }
}
  input[type="checkbox"], input[type="radio"] {
    margin-top: rem(4px);
    opacity: 0;
    cursor: pointer;
    position: absolute;
    + span {
      margin-right: 3px;
      display: inline-block;
      width: 20px;
      height: 20px;
      vertical-align: middle;
      cursor: pointer;
      border: 1px solid $base-border-color;
      background: $light-color;
      .checkbox-checked {
        display: none;
        font-size: 18px;
        color: $gray-dark;
      }
    }
    &:checked + span {
      .checkbox-checked {
        display: block;
      }
    }
  }
  label {
    text-align: left;
  }
}
.text-muted {
  font-size: rem(14px);
}
.done {
  color: $brand-success;
  display: inline-block;
  padding: 0;
  margin-right: rem(15px);
}
.thumb-mask {
  > .mask {
    position: relative;
    img {
      width: 55px;
      height: 55px;
    }
  }
}
.definition-list {
  dl {
    @include display(flex);
    @include flex-wrap(wrap);
    dt {
      font-weight: normal;
    }
    dd,
    dt {
      @include flex(0 0 45%);
      background: $gray-light;
      padding: rem(10px);
      margin: rem(2px);
      &:nth-of-type(even) {
        background: $gray-lighter;
      }
    }
  }
}
.help-block {
  margin-top: $small-space;
}
.btn.disabled,
.btn.disabled:hover {
  background: $gray;
  border-color: $gray;
  color: $gray-dark-color;
}
.alert-warning {
  .material-icons {
    color: $warning;
    font-size: rem(32px);
    margin-right: $small-space;
    padding-top: $extra-small-space;
  }
  .alert-text {
    font-size: rem(15px);
    padding-top: $small-space;
  }
  .alert-link {
    border-radius: 2px;
    border-width: 2px;
    margin-left: $small-space;
    padding: $extra-small-space $medium-space;
    font-weight: 600;
    font-size: rem(13px);
    color: $btn-tertiary-color;
  }
  ul li:last-child .alert-link {
    color: white;
  }
  .warning-buttons {
    margin-top: $extra-small-space;
  }
}
.btn-warning {
  @include transition(all .4s ease-in-out);
}
.btn-tertiary-outline {
  color: $btn-tertiary-color;
  background-image: none;
  background-color: transparent;
  border-color: $btn-tertiary-color;
  border: 0.15rem solid $btn-tertiary-color;
  @include transition(all .4s ease-in-out);
  &:hover {
    border-color: $btn-tertiary-hover;
    color: $btn-tertiary-hover;
  }
}
.alert {
  font-size: rem(13px);
}
.nav-item {
  .nav-link,
  .nav-separtor {
    color: $gray;
    font-weight: bold;
    &.active {
      color: $gray-darker;
    }
  }
}
.separator {
  margin: 0;
  border-color: rgba(0, 0, 0, 0.25);
}
.modal {
  text-align: center;
  padding: 0!important;
}

.modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -4px;
}

.modal-dialog {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
}


